<script>
import successIcon from '../assets/img/success-icon.svg'
export default {
  name: 'SuccessModal',
  props: ['succes-modal-properties'],
  data () {
    return {
      successIcon: successIcon
    }
  },
  methods: {
    redirectToApplicationPage: function () {
      if (this.$props.succesModalProperties.applicationType === '0') {
        this.$router.push('/bireysel-basvuru')
      } else if (this.$props.succesModalProperties.applicationType === '1') {
        this.$router.push('/kurumsal-basvuru')
      } else {
        this.$router.push('/kamu-basvuru')
      }
    }
  }
}
</script>

<template>
  <div class="modal-window position-fixed">
    <div class="col-lg-6 modal-content">
        <div class="modal-icon-box mb-3">
            <img :src="successIcon" alt="">
        </div>
        <div class="modal-message-header mb-3">
            <h4 class="main-head blue">Tebrikler!</h4>
        </div>
        <div class="modal-message-content mb-3">
            e-İmza doğrulama başarılı.
        </div>
        <div>
            <div class="button-group">
              <button @click="redirectToApplicationPage" type="button" class="btn btn-primary">Devam Et</button>
            </div>
        </div>
    </div>
  </div>
</template>

<style scoped>
    .modal-window{
        width: 100vw;
        height: 100vh;
        top: 0;
        left: 0;
        z-index: 1;
        background-color: rgba(0, 0, 0, .8);
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .modal-content{
        padding: 32px;
    }
</style>



