/**
 * Created by erdems on 06.11.2021.
 *
 * Overriding original ArkSigner methods
 */

export const ArkSignerSource = {
  overrideMethods: function (internalPwsigner, pw) {
    internalPwsigner.smartCard.listCertificates = function (callback, config) {
      let uuid = internalPwsigner.registerCallback(callback)

      config.action = pw.prototype.actions.TASK_LIST_CERTIFICATES
      config.licenseKey = internalPwsigner.licenseKey
      config.usedUrl = window.localStorage.getItem('arkSignerUrl')
      config.transactionUUID = uuid
      config.nonce = pw.prototype.nonce()

      let jsonStr = JSON.stringify(config)
      let jsonStrEncoded = btoa(jsonStr)

      internalPwsigner.connector.send(jsonStrEncoded)
    }

    internalPwsigner.smartCard.listTerminals = function (callback, config) {
      let uuid = internalPwsigner.registerCallback(callback)

      config.action = pw.prototype.actions.TASK_LIST_SMART_CARDS
      config.licenseKey = internalPwsigner.licenseKey
      config.usedUrl = window.localStorage.getItem('arkSignerUrl')
      config.transactionUUID = uuid
      config.nonce = pw.prototype.nonce()

      let jsonStr = JSON.stringify(config)
      let jsonStrEncoded = btoa(jsonStr)

      internalPwsigner.connector.send(jsonStrEncoded)
    }
  },

  overrideClientWSURL: function (pw, version) {
    let originalClientURL = 'wss://local.pwsigner.ark-teknoloji.com:16356'
    let newClientURL = version === '2.1.0.5' ? 'ws://localhost:16358' : originalClientURL

    if (window.localStorage.getItem('useAlternativeConnection') && window.localStorage.getItem('useAlternativeConnection').toString().toLowerCase() === 'true') {
      pw.prototype.connectors[pw.prototype.connectorTypes.WEB_SOCKET].clientURL = newClientURL
    } else {
      pw.prototype.connectors[pw.prototype.connectorTypes.WEB_SOCKET].clientURL = originalClientURL
    }
  },

  overrideClientSocketURL: function (pw, version) {
    let originalClientURL = 'https://local.pwsigner.ark-teknoloji.com:16357'
    let newClientURL = version === '2.1.0.5' ? 'http://localhost:16359' : window.localStorage.getItem('contextPath') + '/ark_post/'

    if (window.localStorage.getItem('useAlternativeConnection') && window.localStorage.getItem('useAlternativeConnection').toString().toLowerCase() === 'true') {
      pw.prototype.connectors[pw.prototype.connectorTypes.SOCKETV12].clientURL = newClientURL
      pw.prototype.connectors[pw.prototype.connectorTypes.SOCKETV13].clientURL = newClientURL
    } else {
      pw.prototype.connectors[pw.prototype.connectorTypes.SOCKETV12].clientURL = originalClientURL
      pw.prototype.connectors[pw.prototype.connectorTypes.SOCKETV13].clientURL = originalClientURL
    }
  }
}



