<script>
import backLogo from '../assets/img/back_logo.png'
import successIcon from '../assets/img/success-icon.svg'
import { HTTP } from '../helpers/services'
export default {
  name: 'SuccessPage',
  data () {
    return {
      backLogo: backLogo,
      successIcon: successIcon,
      qnbPayReturnParams: {
        order_no: '',
        order_id: '',
        invoice_id: '',
        status_code: '',
        status_description: '',
        credit_card_no: '',
        transaction_type: '',
        payment_status: '',
        payment_method: '',
        error_code: '',
        error: '',
        auth_code: '',
        merchant_commission: '',
        user_commission: '',
        merchant_commission_percentage: '',
        merchant_commission_fixed: '',
        hash_key: '',
        original_bank_error_code: '',
        original_bank_error_description: ''
      }
    }
  },
  methods: {
    async printReturnParams () {
      console.log(this.$route.query)
      this.qnbPayReturnParams = this.$route.query
      localStorage.setItem('hash_key', this.qnbPayReturnParams.hash_key)
      localStorage.setItem('user_commission', this.qnbPayReturnParams.user_commission)
      localStorage.setItem('merchant_commission', this.qnbPayReturnParams.merchant_commission)
      try {
        await HTTP.post('PrintQnbPaymentDetails', this.$route.query)
      } catch (error) {
        console.log('Ödeme loglama başarisiz', error)
      }
    }
  },
  mounted () {
    this.printReturnParams()
  }
}
</script>
<template>
<div class="col-xl-8 col-lg-10 col-md-12 col-sm-12 col-xs-12 wizard-container m-auto">
    <div class="back-logo-container"><img :src="backLogo" /></div>
    <div class="modal-content" style="padding: 5%;">
        <div class="modal-icon-box">
            <img :src="successIcon" alt="">
        </div>
        <div class="modal-message-header mb-3">
            <h4 class="main-head blue">Teşekkürler!</h4>
            <h5>Ödemeniz başarı ile alınmıştır!</h5>
        </div>
        <div class="form">
            <div class="border-bottom">
              <h4 class="font-color blue">Sipariş Özeti</h4>
            </div>
            <div class="border-bottom form-body p-0">
              <div class="input-group">
                <label>Sipariş Numarası:</label>
                <p>{{qnbPayReturnParams.order_no}}</p>
              </div>
            </div>
            <div class="border-bottom form-body p-0">
              <div class="input-group">
                <label>Ödeme Açıklaması:</label>
                <p>{{qnbPayReturnParams.status_description}}</p>
              </div>
            </div>
          </div>
          <h4 class="font-color blue">Taahhütname sürecine devam edebilmeniz için pop-upı kapatabilirsiniz.</h4>
    </div>
  </div>
</template>

<style scoped>
.wizard-container {
  padding: 118px 96px 96px 96px;
  height: 100vh;
  box-sizing: border-box;
}
</style>



