<script>
import successIcon from '../assets/img/success-icon.svg'
import { HTTP } from '../helpers/services'
import moment from 'moment'
export default {
  name: 'Payment',
  props: ['currentPlan', 'kimlikBilgileri', 'secure3D', 'alertProps', 'paymentDetail', 'parentRef', 'odemeYapildiMiParent'],
  data () {
    return {
      successIcon: successIcon,
      paymentRequest: { // Banka sayfasına yönlendirirken gönderdiğimiz forma ait parametreler
        action: process.env.QNBPAY_URL + '/api/paySmart3D',
        name: '',
        surname: '',
        installments_number: '',
        cc_holder_name: '',
        cc_no: '',
        invoice_description: 'INVOICE TEST DESCRIPTION',
        expiry_month: '',
        expiry_year: '',
        cvv: '',
        return_url: process.env.VUE_URL + 'kepbasvuru/#/payment-onay',
        cancel_url: process.env.VUE_URL + '/kepbasvuru/#/payment-red',
        items: JSON.stringify([{Quantity: 0, qty: 0, name: 'Siparis', price: '', quantity: 1, description: 'KEP00001'}]),
        currency_code: 'TRY'
      },
      selectedInstallment: { // Seçilen taksit ödeme bilgisi
        pos_id: 0,
        campaign_id: 0,
        allocation_id: 0,
        installments_number: 0,
        card_type: '',
        card_program: '',
        card_scheme: '',
        payable_amount: 0,
        hash_key: '',
        amount_to_be_paid: '',
        currency_code: 'TRY',
        currency_id: 0,
        title: ''
      },
      childPaymentDetail: {
        paymentOption: '',
        paymentType: '',
        orderNumber: '',
        paidAmount: '',
        posNumber: process.env.MERCHANT_ID,
        paymentDate: '',
        numberOfInstallments: '0',
        paymentDetails: 'QNBPay Odeme',
        comission: 0, // user_commission
        workPlaceComission: 0 // merchant_commission
      },
      paymentOption: '',
      paymentType: '',
      merchant_key: process.env.MERCHANT_KEY,
      childSecure3D: this.secure3D,
      gettingInstallmentInformationArray: []
    }
  },
  filters: {
    formatCardNumber (value) {
      return value ? value.match(/.{1,4}/g).join(' ') : ''
    }
  },
  methods: {
    updateValue (e) {
      this.paymentRequest.cc_no = e.target.value.replace(/ /g, '')
    },
    async gettingInstallmentInformation () {
      const response = await HTTP.post('GettingInstallmentInformationServlet', {
        credit_card: this.paymentRequest.cc_no.slice(0, 6),
        amount: this.currentPlan.productCharge.totalPrice,
        currency_code: this.paymentRequest.currency_code,
        merchant_key: this.merchant_key,
        is_recurring: 0, // Anlamı bilinmiyor ?
        is_2d: 0
      })
      this.gettingInstallmentInformationArray = response.data.data
      console.log(this.gettingInstallmentInformationArray)
    },
    async getHashAndInvoiceId () {
      if (this.isObjectHasNullValue(this.paymentRequest)) { // İçinde boş bir data var mı kontrol ediyoruz
        this.scrollToTop()
        this.alertProps.isActive = true
        this.alertProps.message = 'Lütfen gerekli yerleri doldurunuz ve taksit bilginizi seçiniz!'
        this.alertProps.cssClass = 'alert alert-danger mt-3'
        return false
      }
      this.$modal.show('secure3dModal')
      const response = await HTTP.post('Get3DTest', { // Hash key ve invoice id'yi üreten servis
        cc_holder_name: this.paymentRequest.cc_holder_name,
        installments_number: this.paymentRequest.installments_number,
        total: this.currentPlan.productCharge.totalPrice
      })
      this.childSecure3D = response.data
      this.$emit('update:secure3D', this.childSecure3D)
      console.log(this.childSecure3D)

      // Salesforce'a atacağımız kaydı setliyoruz
      this.childPaymentDetail.numberOfInstallments = this.paymentRequest.installments_number.toString()
      this.childPaymentDetail.orderNumber = this.childSecure3D.invoice_id
      console.log(this.childPaymentDetail)
      this.$emit('update:paymentDetail', this.childPaymentDetail)

      setTimeout(function () { // Modal render olmadan tetiklenince hata aldığımızdan 1 saniye bekletiyoruz
        this.$refs.submitBtn.click()
      }.bind(this), 1000)
    },
    setAmountPay () {
      setTimeout(function () { // SelectedInstallment ile aynı anda geldiğinden 1 saliselik timout verdik ayrıca propsdan okuyamadığımız parametreleri burda setliyoruz
        this.paymentRequest.items = JSON.stringify([{Quantity: 0, qty: 0, name: 'Siparis', price: this.currentPlan.productCharge.totalPrice, quantity: 1, description: 'KEP00001'}])
        this.paymentRequest.installments_number = this.selectedInstallment.installments_number
        this.paymentRequest.name = this.kimlikBilgileri.ad
        this.paymentRequest.surname = this.kimlikBilgileri.soyad
        console.log(this.paymentRequest)
      }.bind(this), 10)
    },
    otoFillData () {
      if (process.env.EGEMENS_ENV) { // localde dataları otoamtik setlemek için
        this.paymentRequest.cc_holder_name = 'Egemen Şener' // Banka sayfasına yönlendirirken gönderdiğimiz forma ait parametreler
        this.paymentRequest.cc_no = '4022780520669303'
        this.paymentRequest.expiry_month = '01'
        this.paymentRequest.expiry_year = '50'
        this.paymentRequest.cvv = '988'
      }
    },
    goNext () {
      // alert(this.$refs.wizard.activeTabIndex);
      this.$modal.hide('secure3dModal')
      this.parentRef.wizard.nextTab()
    },
    collapsePanelPayment: function (target) {
      // Tüm panelleri kapalı olarak başlatıyoruz
      const allPanels = document.querySelectorAll('#kk_panel, #havalePanel')
      allPanels.forEach(panel => {
        panel.classList.add('panel-hidden')
      })
      // Hedef paneli açıyoruz
      let currentTarget = document.getElementById(target)
      currentTarget.classList.remove('panel-hidden')
      // Diğer panelleri kapatıyoruz
      allPanels.forEach(panel => {
        if (panel !== currentTarget) {
          panel.classList.add('panel-hidden')
        }
      })
      // Toggle logic
      let e = event
      if (e.target.tagName === 'svg') {
        e.target.parentNode.classList.toggle('active')
      } else {
        e.target.classList.toggle('active')
      }
    }
  },
  mounted () {
    this.$modal.hide('secure3dModal')
    this.otoFillData()
  },
  computed: {
    paymentOptions: {
      get () {
        return `${this.paymentOption} ${this.paymentType}` // gereksiz şu anlık incelenmedi
      },
      set (krediKartiMi) { // Havale ve Kredi kartı seçimine göre SF'ye atılacak parametrelerin setlenmesi
        if (krediKartiMi) {
          this.paymentOption = 'Kredi Kartı İle Ödeme'
          this.paymentType = 'Kredi Kartı'
        } else {
          this.paymentOption = 'Havale ile Ödeme'
          this.paymentType = 'EFT/Havale'
          this.childPaymentDetail.orderNumber = 'KEP-' + Date.now() // Şu anlık rastgele setliyoruz
          this.alertProps.isActive = false
        }
        this.childPaymentDetail.paymentOption = this.paymentOption
        this.childPaymentDetail.paymentType = this.paymentType
        this.childPaymentDetail.paymentDate = moment().format('YYYY-MM-DD hh:mm:ss')
        this.childPaymentDetail.paidAmount = this.currentPlan.productCharge.totalPrice
        console.log(this.childPaymentDetail)
        this.$emit('update:paymentDetail', this.childPaymentDetail)
      }
    }
  }
}
</script>
