                <div class="input-group">
              </div>
              <div class="odemeyiTamamlaDiv">
                  <button :disabled="odemeYapildiMiParent" @click="getHashAndInvoiceId" type="button" class="btn btn-primary pl-4 pr-4">Ödemeyi Tamamla</button>
              </div>
              </div>
              </div>
              <div class="havale">
                <div class="input-group">
                  <input :disabled="odemeYapildiMiParent" @click="collapsePanelPayment('havalePanel')" type="radio" name="odeme" id="havale" v-model="paymentOptions" v-bind:value="false">
                  <label for="havale">Havale</label>
                </div>
                <div id="havalePanel" class="panel-hidden">
                  <div class="form">
                    <label class="font-color red" for="hesapBilgileri">Hesap Bilgileri</label>
                    <div class="form-body">
                      <div class="input-group top">
                        <label class="font-color red" for="unvan">Ünvan</label>
                        <input type="text" name="unvan" id="unvan"
                          value="EFİNANS ELEKTRONİK TİC.VE BİLİŞİM HİZ.A.Ş." disabled>
                      </div>
                      <div class="input-group top">
                        <label class="font-color red" for="banka">Banka</label>
                        <input type="text" name="banka" id="banka" value="QNB FİNANSBANK MERKEZ ŞUBE" disabled>
                      </div>
                      <div class="input-group top">
                        <label class="font-color red" for="iban">IBAN</label>
                        <input type="text" name="iban" id="iban" value="TR06 0011 1000 0000 0057 3693 04" disabled>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="form-body">
              <!-- <div class="input-group">
                <input type="text" name="kampanyaKodu" id="kampanyaKodu" placeholder="Kampanya Kodu">
                <button type="button" class="btn btn-primary ml-3 pl-4 pr-4">Uygula</button>
              </div> -->
            </div>
          </div>
          <div class="form col-xl-4 col-lg-4 col-md-6 col-sm-12 col-xs-12 order-sum-box">
            <div class="border-bottom">
              <h4 class="font-color blue">Hesap Özeti</h4>
            </div>
            <div class="border-bottom form-body p-0">
              <div class="input-group">
                <label for="satisTutar">Satış Tutarı</label>
                <input type="text" name="satisTutar" id="satisTutar" :value="currentPlan.productCharge.basePrice + '\ TL'" readonly>
              </div>
              <div class="input-group">
                <label for="kdv">KDV Tutarı %{{ this.kdvCarpani * 100 }}</label>
                <input type="text" name="kdv" id="kdv" :value="currentPlan.productCharge.kdv + '\ TL'" readonly>
              </div>
            </div>
            <div class="border-bottom form-body p-0">
              <div class="input-group">
                <label for="toplamTutar">Toplam Tutar</label>
                <input class="font-color red" type="text" name="toplamTutar" id="toplamTutar" :value="currentPlan.productCharge.totalPrice + '\ TL'"
                  readonly>
              </div>
            </div>
          </div>
          <modal name="secure3dModal" class="secure3dModal" :adaptive="true" :draggable="true" :clickToClose="false">
            <button class="close-icon" @click="goNext()"></button>
            <iframe class="secureFrame" name="output_frame" src="" id="output_frame"></iframe>
            <form :action="paymentRequest.action" method="POST" target="output_frame">
              <input type='hidden' name='cc_no' :value="paymentRequest.cc_no"/>
              <input type='hidden' name='cc_holder_name' :value="paymentRequest.cc_holder_name"/>
              <input type='hidden' name='cvv' :value="paymentRequest.cvv"/>
              <input type='hidden' name='merchant_key' :value="merchant_key"/>
              <input type='hidden' name='expiry_year' :value="paymentRequest.expiry_year"/>
              <input type='hidden' name='currency_code' :value="paymentRequest.currency_code"/>
              <input type='hidden' name='expiry_month' :value="paymentRequest.expiry_month"/>
              <input type='hidden' name='total'  :value="currentPlan.productCharge.totalPrice"/>
              <input type='hidden' name='hash_key' :value="childSecure3D.hash_key"/>
              <input type='hidden' name='surname'  :value="paymentRequest.surname"/>
              <input type='hidden' name='name'  :value="paymentRequest.name"/>
              <input type='hidden' name='invoice_id' :value="childSecure3D.invoice_id"/>
              <input type='hidden' name='invoice_description' :value="paymentRequest.invoice_description"/>
              <input type='hidden' name='return_url' :value="paymentRequest.return_url"/>
              <input type='hidden' name='cancel_url' :value="paymentRequest.cancel_url"/>
              <input type='hidden' name='installments_number'  :value="paymentRequest.installments_number"/>
              <input type='hidden' name='items' :value="paymentRequest.items"/>
              <input type="submit" class="btn btn-primary" visibility: hidden ref="submitBtn" value="ÖDEMEYE YÖNLENDİR">
            </form>
          </modal>
    </div>

</template>
<style scoped>
    .secure3dModal{
      height: 1300px;
      background-color: rgba(0, 0, 0, .8);
    }
    .secure3dModal .secureFrame {
      height: 100%;
      width: 100%;
      border: none;
    }
    .close-icon {
      width: 30px;
      height: 30px;
      position: relative;
      cursor: pointer;
      float: right;
      border: none;
      background: none;
    }
    .close-icon::before, .close-icon::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 20px;
      height: 3px;
      background-color: #870052;
      transform: translate(-50%, -50%);
      transition: transform 0.2s ease;
    }
    .close-icon:before {
      transform: translate(-50%, -50%) rotate(45deg);
    }
    .close-icon:after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
    .odemeyiTamamlaDiv {
      display: flex;
      justify-content: center;
    }
</style>



