function redirectToApplicationPage () {
  if (localStorage.getItem('Basvuru Tipi') === '0') {
    window.location.replace('/kepbasvuru/#/bireysel-basvuru')
  } else if (localStorage.getItem('Basvuru Tipi') === '1') {
    window.location.replace('/kepbasvuru/#/kurumsal-basvuru')
  } else {
    window.location.replace('/kepbasvuru/#/kamu-basvuru')
  }
}
function CustomPopup () {
  const thisVar = this

  this.setKeyboard = function (keyboardPopupParam) {
    this.keyboardPopup = keyboardPopupParam
  }

  this.getKeyboard = function () {
    return this.keyboardPopup
  }

  this.init = function (popupDivElem) {
    this.popupDivElem = popupDivElem
  }

  this.initById = function (id) {
    this.popupDivElem = document.getElementById(id)
  }

  this.close = function () {
    if (this.popupDivElem) {
      if (this.exists() && this.popupDivElem.parentNode) {
        this.popupDivElem.classList.remove('--active')
        thisVar.popupDivElem.parentNode.removeChild(this.popupDivElem)
      }
    }
  }

  this.executeAfterMs = function (msDelay, execFunc) {
    setTimeout(function () {
      execFunc()
    }, msDelay)
  }

  this.initStatus = function (start, end) {
    this.startValue = start
    this.endValue = end
  }

  this.exists = function () {
    return !!document.getElementById(this.popupDivElem.id)
  }

  this.tick = function () {
    if (this.popupDivElem) {
      if (this.exists()) {
        if (this.startValue < this.endValue) {
          this.startValue++
        }

        let progressbarStatusNode = this.popupDivElem.getElementsByClassName('status')
        if (progressbarStatusNode && progressbarStatusNode.length > 0) {
          progressbarStatusNode[0].innerHTML = this.startValue + '/' + this.endValue
        }
      }
    }
  }

  this.findByMsg = function (msg, callback) {
    const parentElem = document.getElementById('message-popup-pile')
    let foundElem = null

    if (parentElem && parentElem.children.length > 0) {
      Array.prototype.forEach.call(parentElem.children, function (childrenElem) {
        if (!foundElem) {
          const popupNum = parseInt(childrenElem.id.split('-')[3], 10)
          const bodyTextElem = document.getElementById('message--popup--body--text-' + popupNum)

          if (bodyTextElem && bodyTextElem.innerHTML === msg) {
            thisVar.init(childrenElem)
            foundElem = thisVar
            callback(foundElem)
          } else if (popupNum === parentElem.children.length) {
            callback(null)
          }
        } else {
          callback(foundElem)
        }
      })
    } else {
      callback(null)
    }
  }
}

const utils = {
  toggleMessagePopup: function (args) {
    let initialDiv = document.getElementById('message-popup-pile')

    if (!initialDiv && window['csdTestScreen']) {
      const initialDivForTest = document.createElement('div')
      initialDivForTest.id = 'message-popup-pile'

      document.body.appendChild(initialDivForTest)
      initialDiv = document.getElementById('message-popup-pile')
    }

    if (initialDiv && initialDiv.children.length > 0) {
      // Remove message popup(s)
      for (const element of initialDiv.children) {
        element.classList.remove('--active')
        initialDiv.removeChild(element)
      }

      show(this)
    } else {
      show(this)
    }

    function show (context) {
      if (initialDiv && initialDiv.children.length === 0) {
        const childNodesNum = initialDiv.children.length + 1

        const mainDiv = document.createElement('div')
        mainDiv.id = 'message--popup--main' + '-' + childNodesNum
        mainDiv.classList.add('custom-popup')
        mainDiv.classList.add('--active')

        const widthAndHeight = context.getWholeScreenMaxWidthAndHeight()
        mainDiv.style.width = widthAndHeight[0] + 'px'
        mainDiv.style.height = widthAndHeight[1] + 'px'

        const combinedDiv = document.createElement('div')
        combinedDiv.id = 'message-popup-combined' + '-' + childNodesNum
        combinedDiv.classList.add('combined')

        const filterDiv = document.createElement('div')
        filterDiv.id = 'message-popup-filter' + '-' + childNodesNum
        filterDiv.classList.add('filter')

        const messagePopup = document.createElement('div')
        messagePopup.id = 'message---popup' + '-' + childNodesNum
        messagePopup.classList.add('actual-popup')

        const localWidthAndHeight = context.getLocalScreenMaxWidthAndHeight()
        messagePopup.style.marginLeft = ((localWidthAndHeight[0] - (widthAndHeight[0] * 30 / 100)) / 2) + 'px'
        messagePopup.style.marginTop = ((localWidthAndHeight[1] - (widthAndHeight[1] * 30 / 100)) / 2) + 'px'

        const headerPopup = document.createElement('div')
        headerPopup.id = 'message--popup--header' + '-' + childNodesNum

        const headerStatusIcon = document.createElement('img')
        if (args.info || (!args.warning && !args.error && !args.success)) {
          headerStatusIcon.src = 'https://upload.wikimedia.org/wikipedia/commons/b/b4/Gtk-dialog-info.svg'
          headerStatusIcon.alt = 'Bilgilendirme'
        } else if (args.warning || (!args.info && !args.error && !args.success)) {
          headerStatusIcon.src = 'https://upload.wikimedia.org/wikipedia/commons/1/17/Warning.svg'
          headerStatusIcon.alt = 'Uyarı'
        } else if (args.error || (!args.info && !args.warning && !args.success)) {
          headerStatusIcon.src = 'https://upload.wikimedia.org/wikipedia/commons/7/75/Erroricon404.PNG'
          headerStatusIcon.alt = 'Hatalı İşlem'
        } else if (args.success || (!args.info && !args.warning && !args.error)) {
          headerStatusIcon.src = 'https://upload.wikimedia.org/wikipedia/commons/3/3f/Commons-emblem-success.svg'
          headerStatusIcon.alt = 'Başarılı İşlem'
        }
        headerStatusIcon.id = 'message--popup--header--status--icon' + '-' + childNodesNum

        const headerTextPopup = document.createElement('span')
        headerTextPopup.innerHTML = args.header
        headerTextPopup.id = 'message--popup--header--text' + '-' + childNodesNum

        const bodyPopup = document.createElement('div')
        bodyPopup.id = 'message--popup--body' + '-' + childNodesNum

        if (args.longMessage) {
          bodyPopup.classList.add('long')
        }

        const bodyTextPopup = document.createElement('span')
        bodyTextPopup.innerHTML = args.body
        bodyTextPopup.id = 'message--popup--body--text' + '-' + childNodesNum
        bodyTextPopup.classList.add('module')
        bodyTextPopup.classList.add('overflow')

        if (args.longMessage) {
          bodyTextPopup.classList.add('more')
        }

        const footerPopup = document.createElement('div')
        footerPopup.id = 'message--popup--footer' + '-' + childNodesNum

        for (let i = 0; i < args.buttonCount; i++) {
          const footerButton = document.createElement('button')
          let buttonTitle

          if (args.cancel) {
            buttonTitle = 'Vazgeç'
            args.cancel = false

            footerButton.classList.add('cancel')
            footerButton.classList.add('secondary-button')

            footerButton.onclick = function () {
              initialDiv = document.getElementById('message-popup-pile')

              // Remove message popup(s)
              for (const element of initialDiv.children) {
                element.classList.remove('--active')
                initialDiv.removeChild(element)
              }
            }
          } else if (args.download) {
            buttonTitle = 'İndir'
            args.download = false

            footerButton.classList.add('download')
            footerButton.classList.add('primary-button')

            footerButton.onclick = function () {
              if (args.callback) {
                args.callback(mainDiv.id)
                // Remove message popup(s)
                for (const element of initialDiv.children) {
                  element.classList.remove('--active')
                  initialDiv.removeChild(element)
                }
              } else {
                console.error('No function is provided for the download action.')
              }
            }
          } else if (args.continue) {
            buttonTitle = 'Devam'
            args.continue = false

            footerButton.classList.add('continue')
            footerButton.classList.add('primary-button')

            footerButton.onclick = function () {
              initialDiv = document.getElementById('message-popup-pile')

              if (args.callback) {
                args.callback()
                // Remove message popup(s)
                for (const element of initialDiv.children) {
                  element.classList.remove('--active')
                  initialDiv.removeChild(element)
                }
              } else {
                // Remove message popup(s)
                for (const element of initialDiv.children) {
                  element.classList.remove('--active')
                  initialDiv.removeChild(element)
                }
              }
            }
          } else if (args.okay) {
            buttonTitle = 'Tamam'
            args.okay = false

            footerButton.classList.add('okay')
            footerButton.classList.add('primary-button')

            footerButton.onclick = function () {
              initialDiv = document.getElementById('message-popup-pile')

              if (args.callback) {
                args.callback()
                // Remove message popup(s)
                for (const element of initialDiv.children) {
                  element.classList.remove('--active')
                  initialDiv.removeChild(element)
                }
              } else {
                redirectToApplicationPage()
                // Remove message popup(s)
                for (const element of initialDiv.children) {
                  element.classList.remove('--active')
                  initialDiv.removeChild(element)
                }
              }
            }
          } else {
            continue
          }

          footerButton.type = 'submit'
          footerButton.innerHTML = buttonTitle
          footerButton.setAttribute('value', buttonTitle)

          footerPopup.appendChild(footerButton)
        }

        headerPopup.appendChild(headerStatusIcon)
        headerPopup.appendChild(headerTextPopup)
        bodyPopup.appendChild(bodyTextPopup)

        messagePopup.appendChild(headerPopup)
        messagePopup.appendChild(bodyPopup)
        messagePopup.appendChild(footerPopup)

        combinedDiv.appendChild(filterDiv)
        combinedDiv.appendChild(messagePopup)
        mainDiv.appendChild(combinedDiv)

        if (initialDiv.children.length === 0) {
          initialDiv.appendChild(mainDiv)
        } else {
          initialDiv.insertBefore(mainDiv, initialDiv.children[0])
        }

        const customPopup = new CustomPopup()
        customPopup.init(mainDiv)

        customPopup.executeAfterMs(100, function () {
          messagePopup.style.marginLeft = Math.max(0, (localWidthAndHeight[0] / 2) - (messagePopup.getBoundingClientRect().width / 2)) + 'px'
          messagePopup.style.marginTop = Math.max(0, (localWidthAndHeight[1] / 2) - (messagePopup.getBoundingClientRect().height / 2)) + 'px'
        })

        return customPopup
      }

      return null
    }
  },

  getWholeScreenMaxWidthAndHeight: function () {
    const width = Math.max(document.body.clientWidth, document.body.scrollWidth, document.body.offsetWidth, window.innerWidth)
    const height = Math.max(document.body.clientHeight, document.body.scrollHeight, document.body.offsetHeight, window.innerHeight)
    return [width, height]
  },

  getLocalScreenMaxWidthAndHeight: function () {
    const width = window.innerWidth
    const height = window.innerHeight
    return [width, height]
  },

  decodeBase64StrAsUTF8: function (base64Str) {
    return decodeURIComponent(atob(base64Str).split('').map(function (c) {
      return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2)
    }).join(''))
  },

  initializeResizing: function () {
    let context = this

    window.onresize = function () {
      const popupMainArr = []
      popupMainArr.push(document.getElementById('message-popup-pile'))

      for (const popupMain of popupMainArr) {
        if (popupMain && popupMain.children.length > 0) {
          for (const popupChild of popupMain.children) {
            const widthAndHeight = context.getWholeScreenMaxWidthAndHeight()
            popupChild.style.width = widthAndHeight[0] + 'px'
            popupChild.style.height = widthAndHeight[1] + 'px'
            // console.debug('Window is resized. Width: ' + popupChild.style.width + ' | Height: ' + popupChild.style.height)

            const actualPopup = popupChild.getElementsByClassName('actual-popup')[0]
            if (actualPopup) {
              const localWidthAndHeight = context.getLocalScreenMaxWidthAndHeight()

              actualPopup.style.marginLeft = Math.max(0, (localWidthAndHeight[0] / 2) - (actualPopup.getBoundingClientRect().width / 2)) + 'px'
              actualPopup.style.marginTop = Math.max(0, (localWidthAndHeight[1] / 2) - (actualPopup.getBoundingClientRect().height / 2)) + 'px'
            }
          }
        }
      }
    }
  }
}

export { utils }



