<template>
  <div :class="this.$props.alertProps.cssClass">
    <p>{{this.$props.alertProps.message}}</p>
  </div>
</template>

<script>
export default {
  name: 'AlertBox',
  props: ['alert-props']
}
</script>

<style scoped>
  .alert {
    color: #818182;
    background-color: #fefefe;
    border-color: #818182;
  }
  .alert.alert-primary {
    color: #004085;
    background-color: #cce5ff;
    border-color: #b8daff;
  }
  .alert.alert-success {
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
  }
  .alert.alert-danger {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb;
  }
  .alert.alert-warning {
    color: #856404;
    background-color: #fff3cd;
    border-color: #ffeeba;
  }
</style>



