<script>
import backLogo from '../assets/img/back_logo.png'
import info from '../assets/img/info.svg'
import AlertBox from '../components/AlertBox'
import Captcha from '../components/Captcha.vue'
import { HTTP } from '../helpers/services'
import NumericKeypad from '../components/NumericKeypad.vue'
import SignerPin from '../components/SignerPin.vue'
import BaseArkSignerComp from '../assets/scripts/ArkSignerComp.js'
import Constants from '../constants.js'

export default {
  name: 'KamuApplicationPage',
  components: {AlertBox, Captcha, NumericKeypad, SignerPin},
  data () {
    return {
      Constants: Constants,
      backLogo: backLogo,
      info: info,
      signerPinProperties: {
        signerTerminals: [],
        taahutname: '',
        checkImzaliMi: false
      },
      currentPlan:
        {
          productType: '',
          productSku: '',
          productName: '',
          productCharge: {
            totalPrice: 0.0
          },
          childProducts: []
        },
      splitedStr: '',
      formData: {
        dealerCode: '',
        kurumBilgileri: [
          {
            vergiNo: '',
            vergiDairesiAdi: '',
            kurumKimlikKodu: '',
            kurumAdi: '',
            birimAdi: '',
            hizmetKodu: '',
            ePosta: '',
            adres: '',
            pk: ''
          }
        ],
        islemYetkilisiBilgileri:
        {
          portalUserName: '',
          tcknYKN: '',
          firstName: '',
          lastName: '',
          unity: '',
          title: '',
          email: '',
          mobile: '',
          phone: '',
          fax: ''
        },
        hesapBilgileri: [
          {
            hesapOid: '',
            crmOid: '',
            portalKullaniciAdi: '',
            alanAdi: Constants.ALAN_ADI,
            hesapOnAdi: '',
            kepAdresi: '',
            hizmetTuru: Constants.HIZMET_TURU_ALMA_GONDERME,
            subdomain: false,
            subdomainValue: ''
          }
        ],
        paymentDetail: {
          paymentOption: '',
          paymentType: '',
          orderNumber: '',
          paidAmount: 0,
          posNumber: '',
          paymentDate: '',
          numberOfInstallments: '',
          paymentDetails: ''
        },
        rehberBilgileri: [
          {
            companyNamePerm: true,
            addressPerm: true,
            searchPermissionInDirectory: true
          }
        ]
      },
      alertProps: {
        message: '',
        cssClass: '',
        isActive: false
      },
      paketler: [],
      taxOff: [],
      cityWithTowns: [],
      selectedCityTowns: [],
      kimlikDogrumu: false,
      kepContacts: [],
      isCaptchaVerified: {
        verified: false,
        captchaCode: '',
        verifyEvent: function (callback) { callback() }
      },
      cardType: false,
      indirildiMi: false,
      taahutnameCheck1: false,
      taahutnameCheck2: false,
      files: [],
      secure3D: {
        invoice_id: '',
        hash_key: ''
      },
      gridIslemYetkilileri: []
    }
  },
  methods: {
    getArkSignerObj (cb) {
      let baseArkSignerComp = new BaseArkSignerComp()
      let arkSignerComp = new baseArkSignerComp.ArkSignerComp()
      let mainObj = Object.assign(arkSignerComp, JSON.parse(window.localStorage.getItem('arkSignerObj')))
      mainObj.construct(mainObj)
      cb(mainObj)
    },
    async getTaahutname () {
      const thiz = this
      const veriTaahutname = JSON.stringify({
        vknTckn: this.formData.kurumBilgileri[0].vergiNo,
        ticariUnvan: this.formData.kurumBilgileri[0].kurumAdi,
        vergiDairesi: this.formData.kurumBilgileri[0].vergiDairesiAdi,
        faturaAdresi: this.formData.kurumBilgileri[0].adres,
        postaKodu: this.formData.kurumBilgileri[0].pk,
        eposta: this.formData.kurumBilgileri[0].ePosta,
        kepAdresi: this.formData.hesapBilgileri[0].alanAdi,
        hesapTipi: this.formData.hesapBilgileri[0].hizmetTuru,
        paketSecimi: this.currentPlan.productName,
        tarifeSecimi: this.currentPlan.productDesc,
        islemYetkilileri: this.kepContacts
      })
      const response = await HTTP.post('getTaahutname', {display: false, formParameters: veriTaahutname, basvuruTipi: 'Kamu'}, {headers: {'Content-Type': 'application/json'}}).catch(e => {
      })
      if (this.signerPinProperties.checkImzaliMi !== true) {
        this.signerPinProperties.taahutname = response.data.data
        this.generateFile(this.signerPinProperties.taahutname, 'KAMU_KEP_BASVURU.pdf')
      } else {
        this.generateFile(this.signerPinProperties.taahutname, 'IMZALI_KEP_BASVURU.pdf')
      }
      this.indirildiMi = true
      if (localStorage.getItem('Kart Tipi') === 'true') {
        Array.prototype.forEach.call(Object.keys(window.arkSignerComp.getTerminals()), function (key) {
          let cardInfo = window.arkSignerComp.getTerminals()[key]
          let certInfo = thiz.decodeBase64StrAsUTF8(window.arkSignerComp.getCertificates()[Object.keys(window.arkSignerComp.getCertificates())[0]].subject)
          thiz.signerPinProperties.signerTerminals.push({'card': atob(cardInfo.terminal), 'cert': certInfo})
        })
      }
    },
    setGridBasvuranIletisimBilgileri () {
      this.kepContacts.forEach(item => {
        let contact = {
          gkullaniciKodu: item.portalUserName,
          gtcKimlikNo: item.tcknYKN,
          gfdAd: item.firstName,
          gfdSoyad: item.lastName,
          gbirim: item.birimAdi,
          gunvan: item.title,
          geposta: item.email,
          gcepTelefonu: item.mobile,
          gtelefon: item.phone,
          gfaks: item.fax
        }
        this.gridIslemYetkilileri.push(contact)
      })
    },
    async kamuHesapAcma () {
      this.setGridBasvuranIletisimBilgileri()
      this.formData.hesapBilgileri[0].crmOid = this.generateCrmOid() // Crm oid'yi üretiyoruz
      var data = {kamuBasvuru: JSON.stringify({
        kurumKimlikKodu: this.formData.kurumBilgileri[0].kurumKimlikKodu,
        hizmetSecimi: this.formData.hesapBilgileri[0].hizmetTuru === Constants.HIZMET_TURU_ALMA ? '1' : '2',
        vergiNo: this.formData.kurumBilgileri[0].vergiNo,
        subdomainVarMi: this.formData.hesapBilgileri[0].subdomain === false ? 0 : 1,
        checkbox: this.formData.rehberBilgileri[0].addressPerm === false ? 0 : 1,
        checkbox1: this.formData.rehberBilgileri[0].companyNamePerm === false ? 0 : 1,
        eposta: this.formData.kurumBilgileri[0].ePosta,
        kurumAdi: this.formData.kurumBilgileri[0].kurumAdi,
        hesapOnAdi: this.formData.hesapBilgileri[0].hesapOnAdi,
        vergiDairesi: this.formData.kurumBilgileri[0].vergiDairesiAdi,
        subdomain: this.formData.hesapBilgileri[0].subdomainValue,
        rgKamuIletisimBilgileri: {
          gridBasvuranIletisimBilgileri: this.gridIslemYetkilileri
        }
      }
      ),
      basvuruAnaEkran: JSON.stringify({
        adres: this.formData.kurumBilgileri[0].adres
      }),
      crmOid: this.formData.hesapBilgileri[0].crmOid
      }
      console.log(data)
      const response = await HTTP.post('KamuHesapAcma', data)
      console.log(response)
      if (response.data.hesapOid) {
        this.formData.hesapBilgileri[0].hesapOid = response.data.hesapOid // Kep'den dönen hesapOid'yi setliyoruz
        this.formData.hesapBilgileri[0].kepAdresi = response.data.kepHesabiAdresi // Kep'den dönen kep hesabı adresini setliyoruz
        return true
      }
      this.scrollToTop()
      this.alertProps.isActive = true
      this.alertProps.message = response.data
      this.alertProps.cssClass = 'alert alert-danger mt-3'
      return false
    },
    async kamuBasvuruGonder () {
      if (await !this.checkTaahutnameProcess()) {
        return false
      }
      await this.kamuHesapAcma()
      let kepApplicationRequest = { kepApplicationRequest: {
        applicationType: 'Kamu',
        isPreApplication: false,
        generalInfo: {
          companyName: this.formData.kurumBilgileri[0].kurumAdi,
          tcknYKN: this.formData.kurumBilgileri[0].vergiNo,
          dealerCode: this.formData.dealerCode,
          taxNumber: this.formData.kurumBilgileri[0].vergiNo,
          taxOfficeName: this.formData.kurumBilgileri[0].vergiDairesiAdi,
          InstitutionIdentityCode: this.formData.kurumBilgileri[0].kurumKimlikKodu, // SOR
          unityName: this.formData.kurumBilgileri[0].birimAdi,
          serviceCode: this.formData.kurumBilgileri[0].hizmetKodu,
          email: this.formData.kurumBilgileri[0].ePosta,
          address: this.formData.kurumBilgileri[0].adres,
          postalCode: this.formData.kurumBilgileri[0].pk
        },
        accountInfo: {
          accountOid: this.formData.hesapBilgileri[0].hesapOid,
          crmOid: this.formData.hesapBilgileri[0].crmOid,
          accountPreName: this.formData.hesapBilgileri[0].hesapOnAdi,
          domainName: this.formData.hesapBilgileri[0].alanAdi,
          serviceType: this.formData.hesapBilgileri[0].hizmetTuru,
          subdomainExist: this.formData.hesapBilgileri[0].subdomain
        },
        kepContacts: this.kepContacts,
        guideInfo: this.formData.rehberBilgileri[0],
        productDetail: {
          productCode: this.currentPlan.productSku,
          channel: ''
        },
        contractDetail: {
          contractType: localStorage.getItem('Kart Tipi') === 'false' ? 'Islak İmza' : 'e-İmza',
          contractVersion: 'DD111',
          contractFile: this.signerPinProperties.taahutname
        } }
      }
      console.log(kepApplicationRequest)
      await this.basvuruGonder(kepApplicationRequest, 'KamuBasvuru')
    }
  },

  mounted () {
    const thiz = this
    thiz.getPakets('KamuProducts')
    thiz.getCityTownTaxOffice()
    thiz.disableInput()
    if (localStorage.getItem('Kart Tipi') === 'true') {
      this.getArkSignerObj(function (obj) {
        window.arkSignerComp = obj
      })
      let btnImzala = document.getElementById('taahutnameImzala')
      let info = document.getElementById('info')
      btnImzala.removeAttribute('hidden')
      info.removeAttribute('hidden')
      this.kurumKamuHesapOnAdiGenerate(this.formData.kurumBilgileri[0].kurumAdi, ' ')
    }
    this.otoFillData(2)
  }
}
</script>

<template>
  <div class="col-xl-8 col-lg-10 col-md-12 col-sm-12 col-xs-12 wizard-container m-auto">
    <div class="back-logo-container"><img :src="backLogo" /></div>
    <form-wizard
      shape="tab"
      color="#870052"
      back-button-text="Geri"
      next-button-text="Devam Et"
      finish-button-text="Başvuruyu Tamamla"
    >
    <div id ="finansLoader" v-bind:hidden="isLoading ? false : true" style="opacity: 80%; pointer-events: none;"><img :src="progressLogo"/>Lütfen Bekleyiniz!</div>
      <p class="text-left" slot="title">
        Aşağıdaki formu eksiksiz doldurarak <strong>"Devam Et"</strong> butonuna
        tıklayınız.
      </p>
      <tab-content title="01. Ürün Seçimi" :before-change="checkCaptchaPackage">
        <div class="row col-12 csc-card pt-4">
          <div class="row csc-card-title border-bottom hor-row">
            <div class="col-6 p-0"><p class="main-head blue">Ürün Seçimi</p></div>
            <div class="col-6 p-0">
              <p class="sub-head text-right">
              </p>
            </div>
          </div>
          <div class="csc-card-body">
            <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps" />
            <p class="sub-head pt-3 pb-3">Size uygun paket seçin.</p>
            <div class="d-flex justify-content-between" style="flex-wrap: wrap;">
              <div v-for="paket in this.paketler" :key="paket.productSku" v-bind:id="paket.productSku"
                class="card col-xl-4 col-lg-4 col-md-4 col-sm-12 col-xs-12 pt-4 pb-4">
                <div class="cards">
                  <div class="card-title border-bottom">
                    <h4 class="main-head blue">{{ paket.productName }}</h4>
                    <h6 class="main-head blue d-flex space-between">
                      <p>{{ paket.productDesc }}</p>
                      <p class="main-head red"><strong>{{ paket.productCharge.totalPrice }} ₺</strong></p>
                    </h6>
                  </div>
                  <div class="card-body">
                    <ul class="feature-list">
                      <li v-for="included in paket.childProducts" v-bind:key="included.childProductDesc"
                        class="d-block">
                        <span>
                          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M6 12L10.2857 16L18 8" stroke="#870052" stroke-width="2" stroke-miterlimit="10" />
                          </svg>
                        </span>
                        <span>{{ included.childProductDesc }}</span>
                    </li>
                    </ul>
                  </div>
                  <div class="card-footer p-0 pb-2 borderless transparent">
                    <div class="button-group p-0 align-center">
                      <button @click="chooseCard(`${paket.productSku}`,paket)" type="button"
                        class="btn btn-primary light w-100">Paketi Seç</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="d-flex form p-0 mt-3 bayiFormArea">
              <div class="form-body p-0">
                <div class="input-group pt-4 pb-4">
                  <label for="bayiKodu">Bayi kodunu giriniz</label>
                  <img :src="info" width="35px" height="35px" title="Güncel bayi kodunuzu bu alana giriniz"/>
                  <input type="text" name="bayiKodu" id="bayiKodu" placeholder="Bayi Kodu" v-model="formData.dealerCode">
                </div>
                <Captcha :isCaptchaVerified.sync="isCaptchaVerified"></Captcha>
              </div>
            </div>
          </div>
          <div class="csc-card-footer"></div>
        </div>
      </tab-content>
      <tab-content title="02. Genel Bilgiler" :before-change="checkFailureClass">
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">1/4</p>
            <p class="main-head blue">Kurum Bilgileri</p>
            <span @click="collapsePanel('kurumBilgileri')" class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div><AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps" />
          <div class="acc-body" id="kurumBilgileri">
            <div class="form">
              <div class="form-body">
                <form>
                  <div class="input-group">
                    <label for="vkn">Vergi No</label>
                    <input @blur="vergiControl(), checkValidation()" @focus="focusElement()" type="number" name="vkn" id="vkn" placeholder="Vergi No" required v-model="formData.kurumBilgileri[0].vergiNo">
                    <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                  </div>
                  <div class="input-group">
                  <label for="vd">Vergi Dairesi Adı</label>
                  <select @change="checkValidation()" @focus="focusElement()" v-model="formData.kurumBilgileri[0].vergiDairesiAdi" name="vd" id="vd">
                    <option v-for="officeInfo in this.taxOff" :key="officeInfo" :value="officeInfo">
                      {{ officeInfo }}</option>
                  </select>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                  <div class="input-group">
                    <label for="mtk">Kurum Kimlik Kodu</label>
                    <input @blur="kurumKoduKontrol()" type="text" name="mtk" id="mtk" placeholder="Kurum Kimlik Kodu" v-model="formData.kurumBilgileri[0].kurumKimlikKodu">
                  </div>
                  <div class="input-group">
                    <label for="merkezIli">Kurum Adı</label>
                    <input @blur="checkValidation(), kurumKamuHesapOnAdiGenerate(formData.kurumBilgileri[0].kurumAdi, ' ')" @focus="focusElement()" type="text" name="kurumAdi" id="kurumAdi" placeholder="Kurum Adı" v-model="formData.kurumBilgileri[0].kurumAdi">
                    <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                  </div>
                  <div class="input-group">
                    <label for="afa">Birim Adı</label>
                    <input @blur="checkValidation()" @focus="focusElement()" type="text" id="afa" placeholder="Birim Adı" v-model="formData.kurumBilgileri[0].birimAdi" />
                    <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                  </div>
                  <div class="input-group">
                    <label for="kurumTel">Hizmet Kodu (Opsiyonel)</label>
                    <input type="text" name="kurumTel" id="kurumTel" placeholder="Hizmet Kodu" v-model="formData.kurumBilgileri[0].hizmetKodu">
                  </div>
                  <div class="input-group">
                    <label for="kurumEposta">e-Posta</label>
                    <input @blur="emailControl(false), checkValidation()" @focus="focusElement()" type="email" name="kurumEposta" id="kurumEposta" placeholder="e-Posta" v-model="formData.kurumBilgileri[0].ePosta">
                    <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                  </div>
                  <div class="input-group">
                    <label for="kurumAdres">Adres</label>
                    <textarea @blur="checkValidation()" @focus="focusElement()" name="kurumAdres" id="kurumAdres" placeholder="Adres" v-model="formData.kurumBilgileri[0].adres"></textarea>
                    <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                  </div>
                  <div class="input-group">
                    <label for="kurumPK">Posta Kodu</label>
                    <input @blur="postCodeControl(), checkValidation()" @focus="focusElement()" type="text" name="kurumPK" id="kurumPK" placeholder="Posta Kodu" v-model="formData.kurumBilgileri[0].pk">
                    <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">2/4</p>
            <p class="main-head blue">İşlem Yetkilisi Bilgileri</p>
            <span @click="collapsePanel('islemYetkilisiBilgileri')" class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <div class="acc-body" id="islemYetkilisiBilgileri">
            <div class="form">
              <div class="form-body">
                <div class="input-group">
                  <label for="yetkiliTCKN">TCKN/Yabancı Kimlik No</label>
                  <input @blur="checkValidation(), kimlikDogrula()" @focus="focusElement()" type="number" name="yetkiliTCKN" id="yetkiliTCKN" placeholder="TCKN/Yabancı Kimlik No" v-model="formData.islemYetkilisiBilgileri.tcknYKN">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliAd">Ad</label>
                  <input @blur="checkValidation()" @focus="focusElement()" type="text" name="yetkiliAd" id="yetkiliAd" placeholder="Ad" v-model="formData.islemYetkilisiBilgileri.firstName">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliSoyad">Soyad</label>
                  <input @blur="checkValidation(), kurumKamuPortalKullaniciAdiUret(formData.islemYetkilisiBilgileri.firstName, formData.islemYetkilisiBilgileri.lastName)" @focus="focusElement()" type="text" name="yetkiliSoyad" id="yetkiliSoyad" placeholder="Soyad" v-model="formData.islemYetkilisiBilgileri.lastName">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="portalKullaniciKodu">Portal Kullanıcı Adı<img :src="info" width="35px" height="35px" style="margin-left: 3%;" title="Hesabınza girerken kullanacağınız kullanıcı adını seçiniz."/></label>
                  <input @blur="checkValidation()" @focus="focusElement()" type="text" name="portalKullaniciKodu" id="portalKullaniciKodu" placeholder="Portal Kullanıcı Adı" disabled v-model="formData.islemYetkilisiBilgileri.portalUserName">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliBirim">Birim (Opsiyonel)</label>
                  <input type="text" name="yetkiliBirim" id="yetkiliBirim" placeholder="Birim" v-model="formData.islemYetkilisiBilgileri.unity">
                </div>
                <div class="input-group">
                  <label for="yetkiliUnvan">Unvan (Opsiyonel)</label>
                  <input type="text" name="yetkiliUnvan" id="yetkiliUnvan" placeholder="Unvan" v-model="formData.islemYetkilisiBilgileri.title">
                </div>
                <div class="input-group">
                  <label for="yetkiliEPosta">e-Posta</label>
                  <input @blur="emailControl(true), checkValidation()" @focus="focusElement()" type="text" name="yetkiliEPosta" id="yetkiliEPosta" placeholder="e-Posta" v-model="formData.islemYetkilisiBilgileri.email">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliCep">Cep Telefonu</label>
                  <input @blur="phoneControl(true), checkValidation()" @focus="focusElement()" @keypress="filter(event)" type="tel" name="yetkiliCep" id="yetkiliCep" v-model="formData.islemYetkilisiBilgileri.mobile">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliTel">Telefon (Opsiyonel)</label>
                  <input type="tel" @keypress="filter(event)" name="yetkiliTel" id="yetkiliTel" v-model="formData.islemYetkilisiBilgileri.phone">
                </div>
                <div class="input-group">
                  <label for="yetkiliFax">Fax</label>
                  <input @blur="faxControl(), checkValidation()" @focus="focusElement()" @keypress="filter(event)" type="tel" name="yetkiliFax" id="yetkiliFax" v-model="formData.islemYetkilisiBilgileri.fax">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="button-group pr-4 border-bottom pb-4">
                  <button type="button" v-on:click="checkAddedPerson" class="btn btn-primary mr-4">Kişiyi Ekle</button>
                </div>
              </div>
              <table class="table table-striped">
                  <thead>
                    <tr>
                      <th scope="col">Yetkili Kişi No</th>
                      <th scope="col">TCKN/Yabancı No</th>
                      <th scope="col">Ad</th>
                      <th scope="col">Soyad</th>
                      <th scope="col">E-posta</th>
                      <th scope="col">Cep Telefonu</th>
                      <th scope="col"></th>
                    </tr>
                  </thead>
                  <tbody>
                      <tr v-for="(contact, index) in this.kepContacts" :key="index">
                          <td>{{ index + 1 }}{{ " ." }}</td>
                          <td>{{ contact.tcknYKN }}</td>
                          <td>{{ contact.firstName }}</td>
                          <td>{{ contact.lastName }}</td>
                          <td>{{ contact.email }}</td>
                          <td>{{ contact.mobile }}</td>
                          <button class="btn btn-primary btn-sm mt-2 mr-4" type="button" v-on:click="removePerson(index)">Kişiyi
                    Kaldır</button>
                      </tr>
                  </tbody>
                </table>
            </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">3/4</p>
            <p class="main-head blue">Hesap Bilgileri</p>
            <span @click="collapsePanel('hesapBilgileri')" class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <div class="acc-body" id="hesapBilgileri">
            <div class="form">
              <div class="form-body">
                <div class="input-group">
                  <label for="hoa">Hesap Ön Adı<img :src="info" width="35px" height="35px" style="margin-left: 3%;" title="BTK tarafından belirlenen standartlar doğrultusunda oluşturulan KEP adresini seçiniz."/></label>
                  <input @blur="kurumsalVeKamuHesapVePortalAdiKontrolEt()" name="hoa" id="hoa" v-model="formData.hesapBilgileri[0].hesapOnAdi" />
                </div>
                <div class="input-group">
                  <label for="alanAdi">Alan Adı</label>
                  <input type="text" name="alanAdi" id="alanAdi" disabled :value="formData.hesapBilgileri[0].subdomainValue + '.' + formData.hesapBilgileri[0].alanAdi">
                </div>
                <div class="input-group radio-container">
                  <label>Subdomain</label>
                  <div class="mr-4">
                    <input @blur="checkValidation()" type="radio" name="subdomain" id="yes" v-model="formData.hesapBilgileri[0].subdomain" v-bind:value="true">
                    <label for="yes">Evet</label>
                  </div>
                  <div>
                    <input  @change="clearSubdomainValue()" type="radio" name="subdomain" id="no" v-model="formData.hesapBilgileri[0].subdomain" v-bind:value="false">
                    <label for="no">Hayır</label>
                  </div>
                </div>
                <div v-if="formData.hesapBilgileri[0].subdomain" class="input-group">
                  <label for="hoa">Subdomain Değeri</label>
                  <input name="hoa" id="hoa" v-model="formData.hesapBilgileri[0].subdomainValue" />
                </div>
                <div class="input-group">
                  <label for="hizmetTuru">Hizmet Türü</label>
                  <select name="hizmetTuru" id="hizmetTuru" v-model="formData.hesapBilgileri[0].hizmetTuru" placeholder="Hizmet Türü">
                    <option :value="Constants.HIZMET_TURU_ALMA">Alma</option>
                    <option :value="Constants.HIZMET_TURU_ALMA_GONDERME">Alma/Gönderme</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">4/4</p>
            <p class="main-head blue">Rehber Bilgileri</p>
            <span @click="collapsePanel('rehberBilgileri')" class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <div class="acc-body" id="rehberBilgileri">
            <div class="form">
              <div class="form-body">
                <div class="input-group radio-container mb-4">
                  <label>Rehberde Aranma</label>
                  <div class="mr-4">
                    <input @change="controlRehberPermissions(true)" type="radio" name="izin" id="evet" v-model="formData.rehberBilgileri[0].searchPermissionInDirectory" v-bind:value="true">
                    <label for="evet">İzin veriyorum</label>
                  </div>
                  <div>
                    <input @change="controlRehberPermissions(false)" type="radio" name="izin" id="hayir" v-model="formData.rehberBilgileri[0].searchPermissionInDirectory" v-bind:value="false">
                    <label for="hayir">İzin vermiyorum</label>
                  </div>
                </div>
                <div class=" checkbox-container" v-bind:hidden="formData.rehberBilgileri[0].searchPermissionInDirectory ? false : true">
                  <label>Rehberde sorgulanmasını istediğiniz alanlar</label>
                  <div class="checkboxes-container">
                      <label for="isim" class="option"><p class="checkboxText">Kurum Adı</p>
                      <input type="checkbox" aria-checked="false" name="isim" id="isim" v-model="formData.rehberBilgileri[0].companyNamePerm"/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="soyisim" class="option"><p class="checkboxText">Adres</p>
                      <input type="checkbox" id="soyisim" name="soyisim" aria-checked="false" v-model="formData.rehberBilgileri[0].addressPerm"/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </tab-content>
      <tab-content title="03. Taahhütname" :before-change="kamuBasvuruGonder">
        <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps"/>
        <NumericKeypad></NumericKeypad>
        <SignerPin :signerPinProp.sync="signerPinProperties" id="signerPin" hidden/>
        <div class="form mb-3">
          <label for="info" id="info" hidden>Taahhütname indirildikten sonra imzalama aşamasına geçilecektir!</label>
          <div class="form-body">
            <div class="input-group long-label">
              <span class="mr-4" style="margin-left: 4%;">
                <button style="all:unset" @click="getTaahutname()">
                <font-awesome-icon class="mr-2" icon="fa-solid fa-file-lines" />Taahhütnameyi indir
                <span>
                  <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M17 11L12 16L7 11" stroke="#870052" stroke-width="2" stroke-miterlimit="10"/>
                    <path d="M12 16L12 4" stroke="#870052" stroke-width="2" stroke-miterlimit="10"/>
                    <path d="M4 20H20" stroke="#870052" stroke-width="2" stroke-miterlimit="10"/>
                  </svg>
                </span>
              </button>
              </span>
              <span id="taahutnameImzala" @click="taahutnameImzala()" hidden>
                <button style="all:unset" v-bind:hidden="indirildiMi ? false : true">
                <font-awesome-icon class="mr-2" icon="fa-solid fa-file-lines" />Taahhütnameyi İmzala
                </button>
              </span>
            </div>
            <div class="taahhut-text" style="margin-top: 1%;">
              <input type="checkbox" name="onayladim" id="onayladim" v-model="taahutnameCheck1">
              <label for="onayladim">Taahhütnameyi okudum ve hesabımın açılmasını onaylıyorum.</label>
            </div>
            <div class="taahhut-text">
              <input type="checkbox" name="saklaİlet" id="saklaİlet" v-model="taahutnameCheck2">
              <label for="saklaİlet">Sakla-İlet yöntemi ile KEP hizmeti almayı kabul ediyorum.</label>
            </div>
            <h6 class="font-color red" style="margin-left: 3%; margin-top: 3%;">TALEP EDİLEN BELGELER</h6>
                <ul class="listed-item" style="margin-left: 3%;">
                  <li>Tüzel kişilerin işlem yetkilisi olarak belirledikleri kişilerin aslı gibidir onaylı atanma yazısı</li>
                  <li>Tüzel kişilerin işlem yetkilisi olarak belirledikleri kişilerin kimlik tespiti için Nüfus cüzdanı, pasaport, sürücü belgesi gibi fotoğraflı ve kimlik yerine geçen geçerli resmi belgenin fotokopisi</li>
                  <li>Noter onaylı imza sirkülerinde, başvuru sahibinin kurumu temsil yetkisi ve ne kadar süre temsil edeceği, imzasının münferiden mi yoksa bir başka temsilci ile birlikte mi geçerli olacağı bilgilerinin yer alması gerekmektedir</li>
                </ul>
                <div class="form-body">
                <p class="info" style="margin-left: 0.5%;"><img :src="info" width="33px" height="33px"/> İşlemlerinizin tamamlanabilmesi için talep edilen ek belgelerinizin ıslak imzalı halinin firmamıza kargolanması gerekmektedir.</p>
                <div class="kargo" style="margin-left: 5%;">
                <p style="font-weight: 500;">Kargo Bilgileri;</p>
                <p>Alıcı adı:     eFinans Elektronik Ticaret ve Bilişim Hizmetleri A.Ş.</p>
                <p>Adres   :     Esentepe Mahallesi Büyükdere Caddesi QNB Finansbank Kristal Kule Binası No:215 Şişli 34394 İstanbul</p>
                <p>İletişim :    0212 370 42 00</p>
                </div>
              </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head blue">EK DOSYA YÜKLEME</p>
          </div>
          <div class="acc-body">
            <div class="form p-0">
              <div class="form-body p-0 pt-4">
                <div class="file-upload input-group mt-4 position-relative">
                  <input class="position-relative"  type="file" name="files" id="files" ref="files" multiple v-on:change="handleFileUpload()">
                  <div class="file-upload-cover position-absolute">
                    <p>
                      <span>
                        <font-awesome-icon class="fa-3x" icon="fa-solid fa-cloud-arrow-up" />
                      </span>
                    </p>
                    <p>Yüklemek istediğiniz dosyayı sürükleyin ya da <strong class="font-color blue">seçin</strong></p>
                    <p class="font-color xs">Desteklenen dosya formatı ".zip"</p>
                  </div>
                </div>
              </div>
                <div v-for="(file, index) in this.files" :key="index">
                <div class="d-flex index">
                  <p class="col-9 p-2 mt-2"> {{ index + 1 }}<span class="dosya">{{ ". Yüklenen Dosya " +
                    file.name }}</span></p>
                  <button class="btn btn-primary btn-sm mt-2 mr-4" type="button" v-on:click="removeFile(index)">Dosyayı Kaldır</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </tab-content>
    </form-wizard>
  </div>
</template>

<style scoped>
.wizard-container {
  padding: 118px 96px 96px 96px;
  height: 100vh;
  box-sizing: border-box;
}
</style>



