<script>
// Developed by Ruşen ASAN
import backLogo from '../assets/img/back_logo.png'
import AlertBox from '../components/AlertBox'
import info from '../assets/img/info.svg'
import Captcha from '../components/Captcha.vue'
import BaseArkSignerComp from '../assets/scripts/ArkSignerComp.js'
import NumericKeypad from '../components/NumericKeypad.vue'
import SignerPin from '../components/SignerPin.vue'
import Datepicker from 'vuejs-datepicker'
import { HTTP } from '../helpers/services'
import {tr} from 'vuejs-datepicker/dist/locale'
import Payment from '../components/Payment.vue'
import Constants from '../constants.js'

export default {
  name: 'EnterpriseApplicationPage',
  components: {AlertBox, Captcha, Payment, Datepicker, NumericKeypad, SignerPin},
  data () {
    return {
      Constants: Constants,
      tr: tr,
      splitedStr: '',
      time1: null,
      uniqueID: '',
      backLogo: backLogo,
      info: info,
      signerPinProperties: {
        signerTerminals: [],
        taahutname: '',
        checkImzaliMi: false
      },
      disabledDates: {
        from: new Date(this.calc18age(), new Date().getMonth(), new Date().getDate())
      },
      currentPlan:
        {
          productType: '',
          productSku: '',
          productName: '',
          productCharge: {
            totalPrice: 0.0
          },
          childProducts: []
        },
      formData: {
        dealerCode: '',
        kurumBilgileri: [
          {
            kurumAdi: '',
            vergiNo: '',
            vergiDairesiAdi: '',
            mtkNo: '',
            merkezinBulunduguIl: '',
            anaFaaliyetAlani: '',
            telefon: '',
            ePosta: '',
            fax: '',
            il: '',
            ilce: '',
            adres: '',
            pk: ''
          }
        ],
        islemYetkilisiBilgileri:
        {
          portalUserName: '',
          nationality: '',
          tcknYKN: '',
          title: '',
          firstName: '',
          lastName: '',
          birthPlace: '',
          birthdate: new Date(this.calc18age(), new Date().getMonth(), new Date().getDate()),
          fathersName: '',
          email: '',
          mobile: ''
        },
        hesapBilgileri: [
          {
            hesapOid: '',
            crmOid: '',
            portalKullaniciAdi: '',
            alanAdi: Constants.ALAN_ADI,
            hesapOnAdi: '',
            kepAdresi: '',
            hizmetTuru: Constants.HIZMET_TURU_ALMA_GONDERME,
            subdomain: false,
            subdomainValue: ''
          }
        ],
        paymentDetail: {
          paymentOption: '',
          paymentType: '',
          orderNumber: '',
          paidAmount: 0,
          posNumber: '',
          paymentDate: '',
          numberOfInstallments: '',
          paymentDetails: '',
          comission: 0, // user_commission
          workPlaceComission: 0 // merchant_commission
        },
        rehberBilgileri: [
          {
            companyNamePerm: true,
            addressPerm: true,
            mainBusinessPerm: true,
            centralProvincePerm: true,
            mtkPerm: true,
            titlePerm: true,
            contactFirstNamePerm: true,
            contactLastNamePerm: true
          }
        ]
      },
      alertProps: {
        message: '',
        cssClass: '',
        isActive: false
      },
      paketler: [],
      taxOff: [],
      cityWithTowns: [],
      selectedCityTowns: [],
      kimlikDogrumu: false,
      kepContacts: [],
      isCaptchaVerified: {
        verified: false,
        captchaCode: '',
        verifyEvent: function (callback) { callback() }
      },
      cardType: false,
      indirildiMi: false,
      taahutnameCheck1: false,
      taahutnameCheck2: false,
      odemeYapildiMi: false,
      files: [],
      secure3D: {
        invoice_id: '',
        hash_key: ''
      },
      gridIslemYetkilileri: []
    }
  },
  methods: {
    getArkSignerObj (cb) {
      let baseArkSignerComp = new BaseArkSignerComp()
      let arkSignerComp = new baseArkSignerComp.ArkSignerComp()
      let mainObj = Object.assign(arkSignerComp, JSON.parse(window.localStorage.getItem('arkSignerObj')))
      mainObj.construct(mainObj)
      cb(mainObj)
    },
    async getTaahutname () {
      const thiz = this
      const veriTaahutname = JSON.stringify({
        vknTckn: this.formData.kurumBilgileri[0].vergiNo,
        ticariUnvan: this.formData.kurumBilgileri[0].kurumAdi,
        ticariSicilNo: this.formData.kurumBilgileri[0].mtkNo,
        vergiDairesi: this.formData.kurumBilgileri[0].vergiDairesiAdi,
        faturaAdresi: this.formData.kurumBilgileri[0].adres,
        postaKodu: this.formData.kurumBilgileri[0].pk,
        ilce: this.formData.kurumBilgileri[0].ilce,
        il: this.formData.kurumBilgileri[0].il,
        merkezIl: this.formData.kurumBilgileri[0].merkezinBulunduguIl.city,
        eposta: this.formData.kurumBilgileri[0].ePosta,
        telefon: this.formData.kurumBilgileri[0].telefon,
        faks: this.formData.kurumBilgileri[0].fax,
        anaFaaliyetAlani: this.formData.kurumBilgileri[0].anaFaaliyetAlani,
        kepAdresi: this.formData.hesapBilgileri[0].alanAdi,
        hesapTipi: this.formData.hesapBilgileri[0].hizmetTuru,
        paketSecimi: this.currentPlan.productName,
        tarifeSecimi: this.currentPlan.productDesc,
        islemYetkilileri: this.kepContacts,
        odemeSekli: this.formData.paymentDetail.paymentType
      })
      const response = await HTTP.post('getTaahutname', {display: false, formParameters: veriTaahutname, basvuruTipi: 'Kurumsal'}, {headers: {'Content-Type': 'application/json'}}).catch(e => {
      })
      if (this.signerPinProperties.checkImzaliMi !== true) {
        this.signerPinProperties.taahutname = response.data.data
        this.generateFile(this.signerPinProperties.taahutname, 'KURUMSAL_KEP_BASVURU.pdf')
      } else {
        this.generateFile(this.signerPinProperties.taahutname, 'IMZALI_KEP_BASVURU.pdf')
      }
      this.indirildiMi = true
      if (localStorage.getItem('Kart Tipi') === 'true') {
        Array.prototype.forEach.call(Object.keys(window.arkSignerComp.getTerminals()), function (key) {
          let cardInfo = window.arkSignerComp.getTerminals()[key]
          let certInfo = thiz.decodeBase64StrAsUTF8(window.arkSignerComp.getCertificates()[Object.keys(window.arkSignerComp.getCertificates())[0]].subject)
          thiz.signerPinProperties.signerTerminals.push({'card': atob(cardInfo.terminal), 'cert': certInfo})
        })
      }
    },
    setgridIslemYetkilileri () {
      this.kepContacts.forEach(item => {
        let contact = {
          gkullaniciKodu: item.portalUserName,
          gad: item.firstName,
          gdogumYeri: item.birthPlace,
          guyruk: item.nationality === Constants.UYRUK_TURK_VATANDASI ? '1' : '0',
          gsoyad: item.lastName,
          gbabaAdi: item.fathersName,
          gdogumTarihi: this.changeFormat(item.birthdate),
          gcepTelefonu: item.mobile,
          gtcKimlikNo: item.tcknYKN,
          geposta: item.email,
          gorevi: item.title
        }
        this.gridIslemYetkilileri.push(contact)
      })
    },
    async kurumsalHesapAcma () {
      this.setgridIslemYetkilileri()
      this.formData.hesapBilgileri[0].crmOid = this.generateCrmOid() // Crm oid'yi üretiyoruz
      var data = {tuzelBasvuru: JSON.stringify({
        tebligatAdresi: 0,
        mtkTicariSicilNo: this.formData.kurumBilgileri[0].mtkNo,
        vergiNo: this.formData.kurumBilgileri[0].vergiNo,
        subdomainVarMi: this.formData.hesapBilgileri[0].subdomain === false ? 0 : 1,
        faks: this.formData.kurumBilgileri[0].fax,
        islemYetkilisiAd: this.formData.rehberBilgileri[0].contactFirstNamePerm === false ? 0 : 1,
        telefon: this.formData.kurumBilgileri[0].telefon,
        islemYetkilisiSoyad: this.formData.rehberBilgileri[0].contactLastNamePerm === false ? 0 : 1,
        adres: this.formData.kurumBilgileri[0].adres,
        email: this.formData.kurumBilgileri[0].ePosta,
        islemYetkilisiUnvan: this.formData.rehberBilgileri[0].titlePerm === false ? 0 : 1,
        kurumAdi: this.formData.kurumBilgileri[0].kurumAdi,
        hizmetTuru: this.formData.hesapBilgileri[0].hizmetTuru === Constants.HIZMET_TURU_ALMA ? '1' : '2',
        hesapOnAdi: this.formData.hesapBilgileri[0].hesapOnAdi,
        merkezinBulunduguIl: this.formData.kurumBilgileri[0].merkezinBulunduguIl.city,
        vergiDairesi: this.formData.kurumBilgileri[0].vergiDairesiAdi,
        anaFaaliyetAlani: this.formData.kurumBilgileri[0].anaFaaliyetAlani,
        subdomain: this.formData.hesapBilgileri[0].subdomainValue,
        postaKodu: this.formData.kurumBilgileri[0].pk,
        rgKurumsalIslemYetkilisi: {
          gridIslemYetkilileri: this.gridIslemYetkilileri
        },
        alanAdi: this.formData.hesapBilgileri[0].alanAdi
      }
      ),
      basvuruAnaEkran: JSON.stringify({
        adres: this.formData.kurumBilgileri[0].adres,
        ilceCmb: this.formData.kurumBilgileri[0].ilce,
        ilCmb: this.formData.kurumBilgileri[0].il
      }),
      crmOid: this.formData.hesapBilgileri[0].crmOid
      }
      console.log(data)
      const response = await HTTP.post('TuzelHesapAcma', data)
      console.log(response)
      if (response.data.hesapOid) {
        this.formData.hesapBilgileri[0].hesapOid = response.data.hesapOid // Kep'den dönen hesapOid'yi setliyoruz
        this.formData.hesapBilgileri[0].kepAdresi = response.data.kepHesabiAdresi // Kep'den dönen kep hesabı adresini setliyoruz
        return true
      }
      this.scrollToTop()
      this.alertProps.isActive = true
      this.alertProps.message = response.data
      this.alertProps.cssClass = 'alert alert-danger mt-3'
      return false
    },
    async kurumsalBasvuruGonder () {
      if (await !this.checkTaahutnameProcess()) {
        return false
      }
      await this.kurumsalHesapAcma()
      let kepApplicationRequest = { kepApplicationRequest: {
        applicationType: 'Kurumsal',
        isPreApplication: false,
        generalInfo: {
          companyName: this.formData.kurumBilgileri[0].kurumAdi,
          taxNumber: this.formData.kurumBilgileri[0].vergiNo,
          tcknYKN: this.formData.kurumBilgileri[0].vergiNo, // SOR
          dealerCode: this.formData.dealerCode,
          taxOfficeName: this.formData.kurumBilgileri[0].vergiDairesiAdi,
          mtkNo: this.formData.kurumBilgileri[0].mtkNo,
          centralProvince: this.formData.kurumBilgileri[0].merkezinBulunduguIl.city,
          mainOperationField: this.formData.kurumBilgileri[0].anaFaaliyetAlani,
          phone: this.formData.kurumBilgileri[0].telefon,
          email: this.formData.kurumBilgileri[0].ePosta,
          fax: this.formData.kurumBilgileri[0].fax,
          city: this.formData.kurumBilgileri[0].il,
          town: this.formData.kurumBilgileri[0].ilce,
          neighbourhood: '', // SOR
          address: this.formData.kurumBilgileri[0].adres,
          postalCode: this.formData.kurumBilgileri[0].pk
        },
        accountInfo: {
          accountOid: this.formData.hesapBilgileri[0].hesapOid,
          crmOid: this.formData.hesapBilgileri[0].crmOid,
          accountPreName: this.formData.hesapBilgileri[0].hesapOnAdi,
          domainName: this.formData.hesapBilgileri[0].alanAdi,
          kepAddress: this.formData.hesapBilgileri[0].kepAdresi,
          serviceType: this.formData.hesapBilgileri[0].hizmetTuru,
          subdomainExist: this.formData.hesapBilgileri[0].subdomain
        },
        kepContacts: this.kepContacts,
        guideInfo: this.formData.rehberBilgileri[0],
        productDetail: {
          productCode: this.currentPlan.productSku,
          channel: ''
        },
        paymentDetail: this.formData.paymentDetail,
        contractDetail: { // Şimdilik Statik Verildi!
          contractType: localStorage.getItem('Kart Tipi') === 'false' ? 'Islak İmza' : 'e-İmza',
          contractVersion: 'DD111',
          contractFile: this.signerPinProperties.taahutname
        } }
      }
      console.log(this.kepContacts)
      console.log(kepApplicationRequest)
      await this.basvuruGonder(kepApplicationRequest, 'KurumsalBasvuru')
    }
  },
  mounted () {
    const thiz = this
    thiz.getPakets('KurumsalProducts')
    thiz.getCityTownTaxOffice()
    thiz.disableInput()
    if (localStorage.getItem('Kart Tipi') === 'true') {
      this.getArkSignerObj(function (obj) {
        window.arkSignerComp = obj
      })
      let btnImzala = document.getElementById('taahutnameImzala')
      let info = document.getElementById('info')
      btnImzala.removeAttribute('hidden')
      info.removeAttribute('hidden')
      this.kurumKamuHesapOnAdiGenerate(this.formData.kurumBilgileri[0].kurumAdi, ' ')
    }
    this.otoFillData(1)
  }
}
</script>
<template>
  <div class="col-xl-8 col-lg-10 col-md-12 col-sm-12 col-xs-12 wizard-container m-auto">
    <div class="back-logo-container"><img :src="backLogo" /></div>
    <form-wizard ref="wizard" shape="tab" color="#870052" back-button-text="Geri" next-button-text="Devam Et"
      finish-button-text="Başvuruyu Tamamla">
      <div id ="finansLoader" v-bind:hidden="isLoading ? false : true" style="opacity: 80%; pointer-events: none;"><img :src="progressLogo"/>Lütfen Bekleyiniz!</div>
      <p class="text-left" slot="title">
        Aşağıdaki formu eksiksiz doldurarak <strong>"Devam Et"</strong> butonuna
        tıklayınız.
      </p>
      <tab-content title="01. Ürün Seçimi" :before-change="checkCaptchaPackage">
        <div class="row col-12 csc-card pt-4">
          <div class="row csc-card-title border-bottom hor-row">
            <div class="col-6 p-0">
              <p class="main-head blue">Ürün Seçimi</p>
            </div>
            <div class="col-6 p-0">
              <p class="sub-head text-right">
              </p>
            </div>
          </div>
          <div class="csc-card-body">
            <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps" />
            <p class="sub-head pt-3 pb-3">Size uygun paket seçin.</p>
            <div class="d-flex justify-content-between" style="flex-wrap: wrap;">
              <div v-for="paket in this.paketler" :key="paket.productSku" v-bind:id="paket.productSku"
                class="card col-xl-4 col-lg-4 col-md-4 col-sm-12 col-xs-12 pt-4 pb-4">
                <div class="cards">
                  <div class="card-title border-bottom">
                    <h4 class="main-head blue">{{ paket.productName }}</h4>
                    <h6 class="main-head blue d-flex space-between">
                      <p>{{ paket.productDesc }}</p>
                      <p class="main-head red"><strong>{{ paket.productCharge.totalPrice }} ₺</strong></p>
                    </h6>
                  </div>
                  <div class="card-body">
                    <ul class="feature-list">
                      <li v-for="included in paket.childProducts" v-bind:key="included.childProductDesc"
                        class="d-block">
                        <span>
                          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M6 12L10.2857 16L18 8" stroke="#870052" stroke-width="2" stroke-miterlimit="10" />
                          </svg>
                        </span>
                        <span>{{ included.childProductDesc }}</span>
                    </li>
                    </ul>
                  </div>
                  <div class="card-footer p-0 pb-2 borderless transparent">
                    <div class="button-group p-0 align-center">
                      <button @click="chooseCard(`${paket.productSku}`,paket)" type="button"
                        class="btn btn-primary light w-100">Paketi Seç</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div id="app">
              <h2></h2>
              <div class="d-flex form p-0 mt-3 bayiFormArea">
                <div class="form-body p-0">
                  <div class="input-group pt-4 pb-4">
                    <label for="bayiKodu">Bayi kodunu giriniz</label>
                    <img :src="info" width="35px" height="35px" title="Güncel bayi kodunuzu bu alana giriniz"/>
                    <input type="text" name="bayiKodu" id="bayiKodu" placeholder="Bayi Kodu" v-model="formData.dealerCode">
                  </div>
                  <Captcha :isCaptchaVerified.sync="isCaptchaVerified"></Captcha>
                </div>
              </div>
            </div>
          </div>
          <div class="csc-card-footer"></div>
        </div>
      </tab-content>
      <tab-content title="02. Genel Bilgiler" :before-change="checkFailureClass">
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">1/4</p>
            <p class="main-head blue">Kurum Bilgileri</p>
            <span @click="collapsePanel('kurumBilgileri')"
              class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps" />
          <div class="acc-body" id="kurumBilgileri">
            <div class="form">
              <div class="form-body">
                <div class="input-group">
                  <label for="kurumAdi">Kurum Adı</label>
                  <input @blur="checkValidation(), kurumKamuHesapOnAdiGenerate(formData.kurumBilgileri[0].kurumAdi, ' ')" @focus="focusElement()" type="text" name="kurumAdi" id="kurumAdi" placeholder="Kurum Adı"
                    v-model="formData.kurumBilgileri[0].kurumAdi">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="vkn">Vergi No</label>
                  <input @blur="vergiControl(), checkValidation()" @focus="focusElement()" type="number" name="vkn" id="vkn" placeholder="Vergi No"
                    v-model="formData.kurumBilgileri[0].vergiNo">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="vd">Vergi Dairesi Adı</label>
                  <select @change="checkValidation()" @focus="focusElement()" v-model="formData.kurumBilgileri[0].vergiDairesiAdi" name="vd"
                    id="vd" placeholder="Vergi Dairesi Adı">
                    <option v-for="officeInfo in this.taxOff" :key="officeInfo" :value="officeInfo">
                      {{ officeInfo }}</option>
                  </select>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="mtk">Mtk No/Ticari Sicil No</label>
                  <input @blur="mtkControl(), checkValidation()" @focus="focusElement()" type="number" name="mtk" id="mtk" placeholder="Mtk No/Ticari Sicil No"
                    v-model="formData.kurumBilgileri[0].mtkNo">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="merkezIli">Merkezin Bulunduğu İl</label>
                  <select @change="checkValidation()" @focus="focusElement()" v-model="formData.kurumBilgileri[0].merkezinBulunduguIl"
                    name="merkezIli" id="merkezIli" placeholder="Merkezin Bulunduğu İl">
                    <option v-for="cityInfo in this.cityWithTowns" :key="cityInfo.city" :value="cityInfo">
                      {{ cityInfo.city }}</option>
                  </select>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="afa">Ana Faaliyet Alanı</label>
                  <input @blur="checkValidation()" @focus="focusElement()" type="text" name="afa" id="afa" placeholder="Ana Faaliyet Alanı"
                    v-model="formData.kurumBilgileri[0].anaFaaliyetAlani">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="kurumTel">Telefon</label>
                  <input @blur="phoneControl(false), checkValidation()" @focus="focusElement()" @keypress="filter(event)" type="number" name="kurumTel" id="kurumTel"
                    v-model="formData.kurumBilgileri[0].telefon" placeholder="(___) ___ __ __">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="kurumEposta">e-Posta</label>
                  <input @blur="emailControl(false), checkValidation()" @focus="focusElement()" type="email" name="kurumEposta" id="kurumEposta" placeholder="e-Posta"
                    v-model="formData.kurumBilgileri[0].ePosta">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="kurumFax">Fax (Opsiyonel)</label>
                  <input type="number" name="kurumFax" id="kurumFax" v-model="formData.kurumBilgileri[0].fax"
                    placeholder="(___) ___ __ __">
                </div>
                <div class="input-group">
                  <label for="kurumIl">İl</label>
                  <select @change="checkValidation(), setCity()" @focus="focusElement()" v-model="selectedCityTowns" name="kurumIl" id="kurumIl"
                    placeholder="İl">
                    <option v-for="cityInfo in this.cityWithTowns" :key="cityInfo.city" :value="cityInfo">
                      {{ cityInfo.city }}</option>
                  </select>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="kurumIlce">İlçe</label>
                  <select @change="checkValidation()" @focus="focusElement()" name="kurumIlce" id="kurumIlce"
                    v-model="formData.kurumBilgileri[0].ilce" placeholder="İlçe">
                    <option v-for="towns in selectedCityTowns.townsOfCity" :key="towns.city">{{ towns }}</option>
                  </select>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="kurumAdres">Adres</label>
                  <textarea @blur="checkValidation()" @focus="focusElement()" name="kurumAdres" id="kurumAdres" placeholder="Adres"
                    v-model="formData.kurumBilgileri[0].adres"></textarea>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="kurumPK">Posta Kodu</label>
                  <input @blur="postCodeControl(), checkValidation()" @focus="focusElement()" type="number" name="kurumPK" id="kurumPK" placeholder="Posta Kodu"
                    v-model="formData.kurumBilgileri[0].pk">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">2/4</p>
            <p class="main-head blue">İşlem Yetkilisi Bilgileri</p>
            <span @click="collapsePanel('islemYetkilisiBilgileri')"
              class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <div class="acc-body" id="islemYetkilisiBilgileri">
            <div class="form">
              <div class="form-body">
                <div class="input-group">
                  <label for="islemYetkilisiUyruk">Uyruk</label>
                  <select @change="checkValidation()" @focus="focusElement()" name="islemYetkilisiUyruk" id="islemYetkilisiUyruk" required
                    v-model="formData.islemYetkilisiBilgileri.nationality">
                    <option :value="null" selected disabled>Uyruk</option>
                    <option :value="Constants.UYRUK_TURK_VATANDASI">Türk Vatandaşı</option>
                    <option :value="Constants.UYRUK_YABANCI">Yabancı</option>
                  </select>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliTCKN">TCKN/Yabancı Kimlik No</label>
                  <input @blur="checkValidation(), kimlikDogrula()" @focus="focusElement()" type="number" name="yetkiliTCKN" id="yetkiliTCKN"
                    placeholder="TCKN/Yabancı Kimlik No" required v-model="formData.islemYetkilisiBilgileri.tcknYKN">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliUnvan">Unvan (Opsiyonel)</label>
                  <input type="text" name="yetkiliUnvan" id="yetkiliUnvan" placeholder="Unvan" required v-model="formData.islemYetkilisiBilgileri.title">
                </div>
                <div class="input-group">
                  <label for="yetkiliAd">Ad</label>
                  <input @blur="checkValidation()" @focus="focusElement()" type="text" name="yetkiliAd" id="yetkiliAd" placeholder="Ad" required
                    v-model="formData.islemYetkilisiBilgileri.firstName">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliSoyad">Soyad</label>
                  <input @blur="checkValidation(), kurumKamuPortalKullaniciAdiUret(formData.islemYetkilisiBilgileri.firstName, formData.islemYetkilisiBilgileri.lastName)" @focus="focusElement()" type="text" name="yetkiliSoyad" id="yetkiliSoyad" placeholder="Soyad"
                    required v-model="formData.islemYetkilisiBilgileri.lastName">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="portalKullaniciKodu">Portal Kullanıcı Kodu<img :src="info" width="35px" height="35px" style="margin-left: 3%;" title="Hesabınza girerken kullanacağınız kullanıcı adını seçiniz."/></label>
                  <input @blur="checkValidation()" type="text" name="portalKullaniciKodu" id="portalKullaniciKodu"
                    placeholder="Portal Kullanıcı Kodu" disabled
                    v-model="formData.islemYetkilisiBilgileri.portalUserName">
                </div>
                <div class="input-group">
                  <label for="yetkiliDY">Doğum Yeri</label>
                  <input @blur="checkValidation()" @focus="focusElement()" type="text" name="yetkiliDY" id="yetkiliDY" placeholder="Doğum Yeri"
                    required v-model="formData.islemYetkilisiBilgileri.birthPlace">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label>Doğum Tarihi</label>
                  <datepicker :language="tr" @select="checkValidation()" @focus="focusElement()" v-model="formData.islemYetkilisiBilgileri.birthdate" format="dd/MM/yyyy" :disabled-dates="disabledDates"></datepicker>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliBabaAdi">Baba Adı</label>
                  <input @blur="checkValidation()" @focus="focusElement()" type="text" name="yetkiliBabaAdi" id="yetkiliBabaAdi"
                    placeholder="Baba Adı" required v-model="formData.islemYetkilisiBilgileri.fathersName">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliEPosta">e-Posta</label>
                  <input @blur="emailControl(true), checkValidation()" @focus="focusElement()" type="email" name="yetkiliEPosta" id="yetkiliEPosta"
                    placeholder="e-Posta" required v-model="formData.islemYetkilisiBilgileri.email">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="yetkiliCep">Cep Telefonu</label>
                  <input @blur="phoneControl(true), checkValidation()" @focus="focusElement()" @keypress="filter(event)" type="number" name="yetkiliCep" id="yetkiliCep" required
                    v-model="formData.islemYetkilisiBilgileri.mobile" placeholder="(___) ___ __ __">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="button-group pr-4">
                  <button type="button" v-on:click="checkAddedPerson" class="btn btn-primary mr-4">Kişiyi Ekle</button>
                </div>
              </div>
              <table class="table table-striped">
                  <thead>
                    <tr>
                      <th scope="col">Yetkili Kişi No</th>
                      <th scope="col">TCKN/Yabancı No</th>
                      <th scope="col">Ad</th>
                      <th scope="col">Soyad</th>
                      <th scope="col">E-posta</th>
                      <th scope="col">Cep Telefonu</th>
                      <th scope="col"></th>
                    </tr>
                  </thead>
                  <tbody>
                      <tr v-for="(contact, index) in this.kepContacts" :key="index">
                          <td>{{ index + 1 }}{{ " ." }}</td>
                          <td>{{ contact.tcknYKN }}</td>
                          <td>{{ contact.firstName }}</td>
                          <td>{{ contact.lastName }}</td>
                          <td>{{ contact.email }}</td>
                          <td>{{ contact.mobile }}</td>
                          <button class="btn btn-primary btn-sm mt-2 mr-4" type="button" v-on:click="removePerson(index)">Kişiyi
                    Kaldır</button>
                      </tr>
                  </tbody>
                </table>
            </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">3/4</p>
            <p class="main-head blue">Hesap Bilgileri</p>
            <span @click="collapsePanel('hesapBilgileri')"
              class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <div class="acc-body" id="hesapBilgileri">
            <div class="form">
              <div class="form-body">
                <div class="input-group">
                  <label for="hoa">Hesap Ön Adı<img :src="info" width="35px" height="35px" style="margin-left: 3%;" title="BTK tarafından belirlenen standartlar doğrultusunda oluşturulan KEP adresini seçiniz."/></label>
                  <input @blur="kurumsalVeKamuHesapVePortalAdiKontrolEt()" name="hoa" id="hoa" v-model="formData.hesapBilgileri[0].hesapOnAdi" />
                </div>
                <div class="input-group">
                  <label for="alanAdi">Alan Adı</label>
                  <input type="text" name="alanAdi" id="alanAdi" disabled :value="formData.hesapBilgileri[0].subdomainValue + '.' + formData.hesapBilgileri[0].alanAdi">
                </div>
                <div class="input-group radio-container">
                  <label>Subdomain</label>
                  <div class="mr-4">
                    <input @blur="checkValidation()" type="radio" name="subdomain" id="yes"
                      v-model="formData.hesapBilgileri[0].subdomain" v-bind:value="true">
                    <label for="yes">Evet</label>
                  </div>
                  <div>
                    <input @change="clearSubdomainValue()" type="radio" name="subdomain" id="no"
                      v-model="formData.hesapBilgileri[0].subdomain" v-bind:value="false">
                    <label for="no">Hayır</label>
                  </div>
                </div>
                <div v-if="formData.hesapBilgileri[0].subdomain" class="input-group">
                  <label for="hoa">Subdomain Değeri</label>
                  <input name="hoa" id="hoa" v-model="formData.hesapBilgileri[0].subdomainValue" />
                </div>
                <div class="input-group">
                  <label for="hizmetTuru">Hizmet Türü</label>
                  <select name="hizmetTuru" id="hizmetTuru"
                    v-model="formData.hesapBilgileri[0].hizmetTuru" placeholder="Hizmet Türü">
                    <option :value="Constants.HIZMET_TURU_ALMA">Alma</option>
                    <option :value="Constants.HIZMET_TURU_ALMA_GONDERME">Alma/Gönderme</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="acc-container checkboxArea">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">4/4</p>
            <p class="main-head blue">Rehber Bilgileri</p>
            <span @click="collapsePanel('rehberBilgileri')"
              class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <div class="acc-body checkBoxBody" id="rehberBilgileri">
            <div class="form">
              <div class="form-body">
                <div class=" checkbox-container">
                  <label>Rehberde sorgulanmasını istediğiniz alanlar</label>
                  <div class="checkboxes-container">
                    <label for="isim" class="option"><p class="checkboxText">Kurum Adı</p>
                      <input type="checkbox" aria-checked="false" name="isim" id="isim" checked disabled />
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="soyisim" class="option"><p class="checkboxText">Adres</p>
                      <input type="checkbox" id="soyisim" name="soyisim" aria-checked="false"  checked disabled/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="has" class="option"><p class="checkboxText">Ana Faaliyet Alanı</p>
                      <input type="checkbox" id="has" name="has" aria-checked="false"  checked disabled />
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="sehir" class="option"><p class="checkboxText">Merkez İl</p>
                      <input type="checkbox" id="sehir" name="sehir" aria-checked="false" checked disabled/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="kaza" class="option"><p class="checkboxText">Mtk/Ticaret Sicil No</p>
                      <input type="checkbox" id="kaza" name="kaza" aria-checked="false" checked disabled/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="titlePerm" class="option"><p class="checkboxText">Unvan</p>
                      <input type="checkbox" id="titlePerm" name="titlePerm" aria-checked="false" v-model="formData.rehberBilgileri[0].titlePerm" />
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="contactFirstNamePerm" class="option"><p class="checkboxText">İşlem Yetkilisi Adı</p>
                      <input type="checkbox" id="contactFirstNamePerm" name="contactFirstNamePerm" aria-checked="false" v-model="formData.rehberBilgileri[0].contactFirstNamePerm" />
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="contactLastNamePerm" class="option"><p class="checkboxText">İşlem Yetkilisi Soyadı</p>
                      <input type="checkbox" id="contactLastNamePerm" name="contactLastNamePerm" aria-checked="false"  v-model="formData.rehberBilgileri[0].contactLastNamePerm"/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </tab-content>
      <tab-content title="03. Ödeme" :before-change="validateHashKey">
        <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps"/>
        <Payment :currentPlan="currentPlan" :kimlikBilgileri="formData.kurumBilgileri[0]" :secure3D.sync="secure3D" :alertProps="alertProps" :paymentDetail.sync ="formData.paymentDetail" :parentRef="$refs" :odemeYapildiMiParent="odemeYapildiMi"></Payment>
      </tab-content>
      <tab-content title="04. Taahhütname" :before-change="kurumsalBasvuruGonder">
        <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps"/>
        <NumericKeypad></NumericKeypad>
        <SignerPin :signerPinProp.sync="signerPinProperties" id="signerPin" hidden/>
        <div class="form mb-3">
          <label for="info" id="info" hidden>Taahhütname indirildikten sonra imzalama aşamasına geçilecektir!</label>
          <div class="form-body">
            <div class="input-group long-label">
              <span class="mr-4" style="margin-left: 4%;">
                <button style="all:unset" @click="getTaahutname()">
                <font-awesome-icon class="mr-2" icon="fa-solid fa-file-lines" />Taahhütnameyi indir
                <span>
                  <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M17 11L12 16L7 11" stroke="#870052" stroke-width="2" stroke-miterlimit="10" />
                    <path d="M12 16L12 4" stroke="#870052" stroke-width="2" stroke-miterlimit="10" />
                    <path d="M4 20H20" stroke="#870052" stroke-width="2" stroke-miterlimit="10" />
                  </svg>
                </span>
              </button>
              </span>
              <span id="taahutnameImzala" @click="taahutnameImzala()" hidden>
                <button style="all:unset" v-bind:hidden="indirildiMi ? false : true">
                <font-awesome-icon class="mr-2" icon="fa-solid fa-file-lines" />Taahhütnameyi İmzala
                </button>
              </span>
            </div>
            <div class="taahhut-text" style="margin-top: 1%;">
              <input type="checkbox" name="onayladim" id="onayladim" v-model="taahutnameCheck1">
              <label for="onayladim">Taahhütnameyi okudum ve hesabımın açılmasını onaylıyorum.</label>
            </div>
            <div class="taahhut-text">
              <input type="checkbox" name="saklaİlet" id="saklaİlet" v-model="taahutnameCheck2">
              <label for="saklaİlet">Sakla-İlet yöntemi ile KEP hizmeti almayı kabul ediyorum.</label>
            </div>
            <h6 class="font-color red" style="margin-left: 3%; margin-top: 3%;">TALEP EDİLEN BELGELER</h6>
                <ul class="listed-item">
                  <li> Tüzel kişinin MERSİS numarası var ise MERSİS numarası kaydı ve numarası</li>
                  <li> Ticaret sicil numarasını içeren, Ticaret Sicil Memurluklarından alınacak Ticaret Sicil Tasdiknamesi veya Faaliyet Belgesi</li>
                  <li> Noter onaylı imza sirkülerinde yer alan yetkilendirmelere uygun olarak imzalanmış ve kaşelenmiş Kurumsal Kayıtlı Elektronik Posta (KEP) Hizmeti Taahhütnamesi ve bu taahhütnamenin eki olarak doldurulmuş olan KEP Yetkilendirme Formu ve Kurumsal KEP Hesabı Başvuru Formu</li>
                  <li> Tüzel kişilerin işlem yetkilisi olarak belirledikleri kişilerin kimlik tespiti için Nüfus cüzdanı, pasaport, sürücü belgesi gibi fotoğraflı ve kimlik yerine geçen geçerli resmi belgenin fotokopisi</li>
                  <li> Noter onaylı imza sirkülerinde, başvuru sahibinin şirketi temsil yetkisi ve ne kadar süre temsil edeceği, imzasının münferiden mi yoksa bir başka temsilci ile birlikte mi geçerli olacağı bilgilerinin yer alması gerekmektedir</li>
                  <li> Başvuru sahibinin şirketin kanunî temsilcisi olduğu ilan edilen, imza sirkülerinin ilan edildiği ticaret sicil gazetesi</li>
                  <li> Şirket Vergi Levhası fotokopisi</li>
                </ul>
                <div class="form-body">
                <p class="info" style="margin-left: 0.5%;"><img :src="info" width="33px" height="33px"/> İşlemlerinizin tamamlanabilmesi için talep edilen ek belgelerinizin ıslak imzalı halinin firmamıza kargolanması gerekmektedir.</p>
                <div class="kargo" style="margin-left: 5%;">
                <p style="font-weight: 500;">Kargo Bilgileri;</p>
                <p>Alıcı adı:     eFinans Elektronik Ticaret ve Bilişim Hizmetleri A.Ş.</p>
                <p>Adres   :     Esentepe Mahallesi Büyükdere Caddesi QNB Finansbank Kristal Kule Binası No:215 Şişli 34394 İstanbul</p>
                <p>İletişim :    0212 370 42 00</p>
                </div>
              </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head blue">EK DOSYA YÜKLEME</p>
          </div>
          <div class="acc-body">
            <div class="form p-0">
              <div class="form-body p-0 pt-2">
                <div class="file-upload input-group mt-4 position-relative">
                  <input class="position-relative"  type="file" name="files" id="files" ref="files" multiple v-on:change="handleFileUpload()">
                  <div class="file-upload-cover position-absolute">
                    <p>
                      <span>
                        <font-awesome-icon class="fa-3x" icon="fa-solid fa-cloud-arrow-up" />
                      </span>
                    </p>
                    <p>Yüklemek istediğiniz dosyayı sürükleyin ya da <strong class="font-color blue">seçin</strong></p>
                    <p class="font-color xs">Desteklenen dosya formatı ".zip"</p>
                  </div>
                </div>
              </div>
                <div v-for="(file, index) in this.files" :key="index">
                <div class="d-flex index">
                  <p class="col-9 p-2 mt-2"> {{ index + 1 }}<span class="dosya">{{ ". Yüklenen Dosya " +
                    file.name }}</span></p>
                  <button class="btn btn-primary btn-sm mt-2 mr-4" type="button" v-on:click="removeFile(index)">Dosyayı Kaldır</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </tab-content>
    </form-wizard>
  </div>
</template>

<style scoped>
.wizard-container {
  padding: 118px 96px 96px 96px;
  height: 100vh;
  box-sizing: border-box;
}
</style>



