<script>
import backLogo from '../assets/img/back_logo.png'
import failIcon from '../assets/img/fail-icon.svg'
import { HTTP } from '../helpers/services'
export default {
  name: 'FailPage',
  data () {
    return {
      backLogo: backLogo,
      failIcon: failIcon,
      qnbPayReturnParams: {
        order_no: '',
        order_id: '',
        invoice_id: '',
        status_code: '',
        status_description: '',
        credit_card_no: '',
        transaction_type: '',
        payment_status: '',
        payment_method: '',
        error_code: '',
        error: '',
        auth_code: '',
        merchant_commission: '',
        user_commission: '',
        merchant_commission_percentage: '',
        merchant_commission_fixed: '',
        hash_key: '',
        original_bank_error_code: '',
        original_bank_error_description: ''
      }
    }
  },
  methods: {
    async printReturnParams () {
      console.log(this.$route.query)
      this.qnbPayReturnParams = this.$route.query
      try {
        await HTTP.post('PrintQnbPaymentDetails', this.$route.query)
      } catch (error) {
        console.log('Ödeme loglama başarisiz', error)
      }
    }
  },
  mounted () {
    this.printReturnParams()
  }
}
</script>
<template>
<div class="col-xl-8 col-lg-10 col-md-12 col-sm-12 col-xs-12 wizard-container m-auto">
    <div class="back-logo-container"><img :src="backLogo" /></div>
    <div class="modal-content" style="padding: 5%;">
        <div class="modal-icon-box">
            <img :src="failIcon" alt="">
        </div>
        <div class="modal-message-header mb-3">
            <h4 class="main-head blue">Hata!</h4>
            <h5>Ödemeniz alınırken bir hata ile karşılaşıldı!</h5>
            <h4>{{qnbPayReturnParams.error}}</h4>
            <h5>Kredi Kartı No: {{qnbPayReturnParams.credit_card_no}}</h5>
            <h5>Sipariş No: {{qnbPayReturnParams.order_id}}</h5>
            <h5>Fatura No: {{qnbPayReturnParams.invoice_id}}</h5>
        </div>
        <div class="form">
            <div>
              <h4 class="font-color blue">Lütfen işlemi tekrar deneyeniz. Ödediğinizi düşünüyorsanız bizimle iletişime geçiniz!</h4>
            </div>
        </div>
        <h4 class="font-color blue">Bu pop-up'ı kapatabilirsiniz!</h4>
    </div>
  </div>
</template>

<style scoped>
.wizard-container {
  padding: 118px 96px 96px 96px;
  height: 100vh;
  box-sizing: border-box;
}
</style>



