  this.ArkSignerComp.prototype.PAdESSignFunc = function (params, initializeAction, finalizeAction, cb) {
    /* eslint-disable-line */ let successMessage = thiz.config.successMessage || 'İmzalama işlemi başarılı şekilde tamamlandı!'
    let errorMessage = thiz.config.errorMessage || 'İmza oluşturulurken bir hata oluştu'
    let progressMessage = thiz.config.progressMessage || 'İmza atılıyor.. Lütfen Bekleyiniz...'
    let certVal = thiz.getSelectedCertificate()
    let expiredCheck = Base64.decode(certVal['isExpired'])
    if (expiredCheck && expiredCheck.toString().toLowerCase() === 'true') {
      let afterDate = Base64.decode(certVal['notAfter'])
      let timeOperatorIndex = afterDate.indexOf('GMT')
      afterDate = afterDate.slice(0, timeOperatorIndex) + ' tarihi ile seçili sertifikanın kullanım süresi dolmuştur. Lütfen seçili sertifikanızı yenileyiniz!'
      let args = {
        okay: true,
        header: 'Mali Mühür/E-İmza İmzalama Aracı',
        body: afterDate,
        buttonCount: 1,
        error: true
      }
      utils.toggleMessagePopup(args)
      return
    }

    initializeAction = 'pades-initialize-signing'
    finalizeAction = 'pades-finalize-signing'
    certVal = thiz.hexToBase64(certVal['certificateHex'])

    let args = {
      okay: true,
      header: 'Mali Mühür/E-İmza İmzalama Aracı',
      body: progressMessage,
      buttonCount: 1,
      info: true
    }
    utils.toggleMessagePopup(args)

    $.ajax({
      url: thiz.config.requestUrl,
      method: 'POST',
      data: {
        'action': initializeAction,
        'certBase64': certVal,
        'data': params.data,
        'timestamped': !params.timestamped
      },
      dataType: 'json',
      success: function (resultInitializeSigning) {
        if (resultInitializeSigning.success === true) {
          let digestToSignBase64 = resultInitializeSigning.digestBase64

          internalPwsigner.cades.signDigest(function (code, json) {
            let obj = JSON.parse(json)
            let cadesSignatureBase64 = obj[0].signedMime

            if ((typeof (code) === 'string' ? parseInt(code) : code) === PWSigner.prototype.codes.RESPONSE_SUCCESSFUL) {
              $.ajax({
                url: thiz.config.requestUrl,
                method: 'POST',
                data: {
                  'action': finalizeAction,
                  'transactionUUID': resultInitializeSigning.transactionUUID,
                  'signatureBase64': cadesSignatureBase64,
                  'timestamped': !params.timestamped
                },
                dataType: 'json',
                success: function (resultFinalizeSigning) {
                  if (thiz.progressBar) {
                    thiz.progressBar.close()
                  }

                  if (resultFinalizeSigning.success === true) {
                    cb(resultFinalizeSigning)
                  } else {
                    let args = {
                      okay: true,
                      header: 'Mali Mühür/E-İmza İmzalama Aracı',
                      body: errorMessage,
                      buttonCount: 1,
                      error: true
                    }
                    utils.toggleMessagePopup(args)
                  }
                },
                error: function (_resultFinalizeSigning) {
                  if (thiz.progressBar) {
                    thiz.progressBar.close()
                  }

                  let args = {
                    okay: true,
                    header: 'Mali Mühür/E-İmza İmzalama Aracı',
                    body: errorMessage,
                    buttonCount: 1,
                    error: true
                  }
                  utils.toggleMessagePopup(args)
                }
              })
            } else {
              if (thiz.progressBar) {
                thiz.progressBar.close()
              }
              thiz.showError(code)
            }
          }, {
            library: thiz.library,
            slotId: thiz.slotId,
            certSerialNumber: thiz.serialNumber,
            dataBase64: digestToSignBase64,
            pincode: params.pincode,
            addSigningTime: false
          })
        } else {
          if (thiz.progressBar) {
            thiz.progressBar.close()
          }

          let args = {
            okay: true,
            header: 'Mali Mühür/E-İmza İmzalama Aracı',
            body: errorMessage,
            buttonCount: 1,
            error: true
          }
          utils.toggleMessagePopup(args)
        }
      },
      error: function (_result) {
        if (thiz.progressBar) {
          thiz.progressBar.close()
        }

        let args = {
          okay: true,
          header: 'Mali Mühür/E-İmza İmzalama Aracı',
          body: errorMessage,
          buttonCount: 1,
          error: true
        }
        utils.toggleMessagePopup(args)
      }
    })
  }

  this.ArkSignerComp.prototype.XAdESSignFunc = function (params, initializeAction, finalizeAction, cb) {
    /* eslint-disable-line */ let successMessage = thiz.config.successMessage || 'İmzalama işlemi başarılı şekilde tamamlandı!'
    let errorMessage = thiz.config.errorMessage || 'İmza oluşturulurken bir hata oluştu'
    let progressMessage = thiz.config.progressMessage || 'İmza atılıyor.. Lütfen Bekleyiniz...'
    let certVal = thiz.getSelectedCertificate()
    let expiredCheck = Base64.decode(certVal['isExpired'])
    if (expiredCheck && expiredCheck.toString().toLowerCase() === 'true') {
      let afterDate = Base64.decode(certVal['notAfter'])
      let timeOperatorIndex = afterDate.indexOf('GMT')
      afterDate = afterDate.slice(0, timeOperatorIndex) + ' tarihi ile seçili sertifikanın kullanım süresi dolmuştur. Lütfen seçili sertifikanızı yenileyiniz!'
      let args = {
        okay: true,
        header: 'Mali Mühür/E-İmza İmzalama Aracı',
        body: afterDate,
        buttonCount: 1,
        error: true
      }
      utils.toggleMessagePopup(args)

      return
    }
    certVal = thiz.hexToBase64(certVal['certificateHex'])
    let args = {
      okay: true,
      header: 'Mali Mühür/E-İmza İmzalama Aracı',
      body: progressMessage,
      buttonCount: 1,
      info: true
    }
    utils.toggleMessagePopup(args)
    let initializeParams = {
      'action': initializeAction,
      'certBase64': certVal,
      'data': params.data,
      'timestamped': !params.timestamped
    }
    if (initializeAction === 'cades-initialize-signing') {
      if (!thiz.config.isAttachCadesSign || thiz.config.isAttachCadesSign === false) {
        initializeParams['attached'] = false
      } else {
        initializeParams['attached'] = true
      }
    }
    $.ajax({
      url: thiz.config.requestUrl,
      method: 'POST',
      data: initializeParams,
      dataType: 'json',
      success: function (result) {
        if (result.success === true) {
          /* ADIM 3 - sunucudan gelen digest bilgisini al */
          let digestToBeSigned = result['digestBase64']
          let transactionUUID = result['transactionUUID']

          try {
            /* ADIM 4 - digest bilgisini imzala (raw imza) */
            /* bu kısım kritik - sadece normal imza attırıyoruz */
            internalPwsigner.smartCard.signPKCS1Padding1(function (code, json) {
              if ((typeof (code) === 'string' ? parseInt(code) : code) === PWSigner.prototype.codes.RESPONSE_SUCCESSFUL) {
                let signingResponse = internalPwsigner.parseJSON(json)
                let signatureBase64 = signingResponse[0].signature

                /* ADIM 5 - imzalanan signedAttributes bilgisini */
                /* sunucuya gönder, imzalama işlemini tamamlat */

                $.ajax({
                  url: thiz.config.requestUrl,
                  method: 'POST',
                  data: {
                    'action': finalizeAction,
                    'transactionUUID': transactionUUID,
                    'signatureBase64': signatureBase64,
                    'timestamped': !params.timestamped
                  },
                  dataType: 'json',
                  success: function (result) {
                    if (thiz.progressBar) {
                      thiz.progressBar.close()
                    }
                    if (result.success === true) {
                      cb(result)
                    } else {
                      let args = {
                        okay: true,
                        header: 'Mali Mühür/E-İmza İmzalama Aracı',
                        body: errorMessage,
                        buttonCount: 1,
                        error: true
                      }
                      utils.toggleMessagePopup(args)
                    }
                  },
                  error: function (_result) {
                    if (thiz.progressBar) {
                      thiz.progressBar.close()
                    }

                    let args = {
                      okay: true,
                      header: 'Mali Mühür/E-İmza İmzalama Aracı',
                      body: errorMessage,
                      buttonCount: 1,
                      error: true
                    }
                    utils.toggleMessagePopup(args)
                  }
                })
              } else {
                if (thiz.progressBar) {
                  thiz.progressBar.close()
                }
                thiz.showError(code)
              }
            }, {
              library: thiz.library,
              slotId: thiz.slotId,
              certSerialNumber: thiz.serialNumber,
              dataBase64: digestToBeSigned,
              pincode: params.pincode,
              isAttached: true,
              addSigningTime: true
            })
          } catch (err) {
            if (thiz.progressBar) {
              thiz.progressBar.close()
            }

            let args = {
              okay: true,
              header: 'Mali Mühür/E-İmza İmzalama Aracı',
              body: errorMessage,
              buttonCount: 1,
              error: true
            }
            utils.toggleMessagePopup(args)
          }
        } else {
          if (thiz.progressBar) {
            thiz.progressBar.close()
          }

          let args = {
            okay: true,
            header: 'Mali Mühür/E-İmza İmzalama Aracı',
            body: errorMessage,
            buttonCount: 1,
            error: true
          }
          utils.toggleMessagePopup(args)
        }
      },
      error: function (_result) {
        if (thiz.progressBar) {
          thiz.progressBar.close()
        }

        let args = {
          okay: true,
          header: 'Mali Mühür/E-İmza İmzalama Aracı',
          body: errorMessage,
          buttonCount: 1,
          error: true
        }
        utils.toggleMessagePopup(args)
      }
    })
  }

