import Card from '../components/Card'
import Footer from '../components/Footer'
import backLogo from '../assets/img/back_logo.png'
import hemenBasvur from '../assets/img/hemen-basvur.png'
import onlineBasvur from '../assets/img/online-basvur.png'
import { HTTP } from '../helpers/services'
import { version } from "../../package.json"

export default {
  name: 'LandingPage',
  components: {
    Card,
    Footer
  },
  data () {
    return {
      backLogo: backLogo,
      hemenBasvur: hemenBasvur,
      onlineBasvur: onlineBasvur,
      onlineBasvuru: {
        isTitleHasIcon: true,
        bodyImg: onlineBasvur,
        bodyTitle: 'Online Başvuru',
        bodyText: 'e-İmza ile hızlı ve kolay bir şekilde başvuru yapmak için tıklayınız.',
        isBodyHasIcon: true
      },
      islakImzaBasvur: {
        isTitleHasIcon: true,
        bodyImg: hemenBasvur,
        bodyTitle: 'Hemen Başvur',
        bodyText: 'Islak imza ile başvuru yapmak için tıklayınız.',
        isBodyHasIcon: true
      },
      servisSonucu: []
    }
  },
  methods: {
    setCardType: function (cardType) {
      localStorage.setItem('Kart Tipi', cardType)
    }
  },
  mounted () {
    localStorage.removeItem('Kart Tipi')
    console.log("Project version is :" + version)
  }
}
</script>

