<script>
import { HTTP } from '../helpers/services'
import AlertBox from '../components/AlertBox'
export default {
  components: {AlertBox},
  name: 'SignerPin',
  props: ['signerPinProp'],
  data () {
    return {
      alertProps: {
        message: '',
        cssClass: '',
        isActive: false
      },
      signerTerminals: this.signerPinProp,
      pinKd: '',
      message: this.currentDateTime() + ' tarihinde KEP Taahütnamemi imzalamak istiyorum.'
    }
  },
  mounted () {},
  methods: {
    currentDateTime () {
      const current = new Date()
      const date = `${current.getDate()}/${current.getMonth() + 1}/${current.getFullYear()}`
      const time = current.getHours() + ':' + current.getMinutes() + ':' + current.getSeconds()
      const dateTime = date + ' ' + time
      return dateTime
    },
    async signPdf (pinKodu) {
      const params = new URLSearchParams()
      params.append('action', 'pades-initialize-signing')
      params.append('certBase64', window.arkSignerComp.hexToBase64(window.arkSignerComp.getCertificates()[Object.keys(window.arkSignerComp.getCertificates())[0]].certificateHex))
      params.append('data', this.signerTerminals.taahutname)
      params.append('timestamped', 'false')
      const config = {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }
      const response = await HTTP.post('https://qnbfinansbank.e-defteryukleme.com/edefteryukle/ark/ark.signer.pades/view/pades', params, config)

      const params2 = new URLSearchParams()
      params2.append('callback', 'cb')
      params2.append('parameters', btoa('{"library":"' + atob(window.arkSignerComp.getTerminals()[Object.keys(window.arkSignerComp.getTerminals())[0]].library) + '","slotId":"' + atob(window.arkSignerComp.getTerminals()[Object.keys(window.arkSignerComp.getTerminals())[0]].slotId) + '","certSerialNumber":"' + atob(window.arkSignerComp.getCertificates()[Object.keys(window.arkSignerComp.getCertificates())[0]].serialNumber) + '","dataBase64":"' + response.data.digestBase64 + '","pincode":"' + pinKodu + '","addSigningTime":"false","transactionUUID":"' + response.data.transactionUUID + '","nonce":"247k9k786n83889q2z9b0v5l2g8j4o4lvwyvikrd15wrf14mk96q8hg1ilzhcdhiss0qyq06hxpy8ipxa0up1a22uu8yp65bzc2ib6lrrzpgln1pvss211e3eaas0kzyly7smh4blo0qy","usedUrl":"https://portaltest.efinans.com.tr/","licenseKey":"' + window.arkSignerComp.getLicense() + '","action":"signCAdES_BES_Digest"}'))

      let response2 = await HTTP.post('https://local.pwsigner.ark-teknoloji.com:16357', params2, config)
      response2 = JSON.parse(response2.data.replace('cb(', '').replace(')', ''))[0]

      if (response2.responseCode === '0') {
        const params3 = new URLSearchParams()
        params3.append('action', 'pades-finalize-signing')
        params3.append('transactionUUID', response2.transactionUUID)
        params3.append('signatureBase64', response2.signedMime)
        params3.append('timestamped', 'false')

        const response3 = await HTTP.post('https://qnbfinansbank.e-defteryukleme.com/edefteryukle/ark/ark.signer.pades/view/pades', params3, config)

        if (response3.data.success) {
          let signerInfo = document.getElementById('signerPin')
          signerInfo.setAttribute('hidden', true)
          this.alertProps.isActive = false
          this.signerTerminals.taahutname = response3.data.signedBase64
          this.$emit('update:signerPinProp.taahutname', this.signerTerminals.taahutname)
          this.generateFile(this.signerTerminals.taahutname, 'IMZALI_KEP_BASVURU.pdf')
          this.signerTerminals.checkImzaliMi = true
        }
      } else {
        this.alertProps.isActive = true
        this.alertProps.message = 'Lütfen! Pin Kodunun Doğru Olduğundan Emin Olunuz!'
        this.alertProps.cssClass = 'alert alert-danger mt-3'
      }
    }
  }
}
</script>

<template>
  <div class="modal-window position-fixed">
    <div class="col-lg-6 modal-content">
      <div class="d-flex form p-0">
        <div class="form-body p-0">
          <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps"/>
           <label>{{ message }}</label>
          <div class="input-group">
              <label for="eimzaInfo">Kart Seçiniz:</label>
              <select name="arkSigner" id="arkSigner">
                <option v-for="(item,index) in JSON.parse(JSON.stringify(this.signerTerminals))['signerTerminals']" :value="index" :key="item.card">{{ item.card }}</option>
              </select>
            </div>
            <div class="input-group">
              <label for="certInfo">Sertifika Seçiniz:</label>
              <select name="cert" id="cert">
                <option v-for="(item,index) in JSON.parse(JSON.stringify(this.signerTerminals))['signerTerminals']" :value="index" :key="item.cert">{{ item.cert }}</option>
              </select>
            </div>
          </div>
        </div>
        <div class="d-flex form p-0">
          <div class="form-body p-0">
            <div class="input-group pt-3 pb-2">
            <label for="pinKod">Şifre:</label>
            <input class="enterPin" type="password" name="pinKodu" id="pinKodu" v-model="pinKd">
          </div>
          </div>
        </div>
        <div>
            <div class="button-group">
              <button @click="signPdf(pinKd)" type="button" class="btn btn-primary">Tamam</button>
            </div>
        </div>
    </div>
  </div>
</template>

<style scoped>
    .modal-window{
        width: 70vw;
        height: 70vh;
        top: 0;
        left: 0;
        z-index: 1;
        background-color: rgba(0, 0, 0, .4);
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: 15vw;
        margin-top: 5vw;
    }
    .modal-content{
        padding: 32px;
    }
</style>



