<script>
import backLogo from '../assets/img/back_logo.png'

export default {
  name: 'ImzaPage',
  data () {
    return {
      meta: [
        { charset: 'utf-8' }
      ],
      backLogo: backLogo,
      applicationType: 'Başvuru Tipi'
    }
  },
  methods: {
    setApplicationType: function (btnId) {
      let btnGo = document.getElementById('btnGo')
      this.applicationType !== 'Başvuru Tipi' ? btnGo.removeAttribute('disabled') : btnGo.addAttribute('disabled')
      localStorage.setItem('Basvuru Tipi', this.applicationType)
    },
    redirectToPage: function () {
      if (localStorage.getItem('Kart Tipi') === 'true') {
        this.$router.push('/e-imza-ile-dogrula')
      } else if (localStorage.getItem('Basvuru Tipi') === '0') {
        this.$router.push('/bireysel-basvuru')
      } else if (localStorage.getItem('Basvuru Tipi') === '1') {
        this.$router.push('/kurumsal-basvuru')
      } else {
        this.$router.push('/kamu-basvuru')
      }
    }
  },
  mounted () {}
}
</script>

<template>
  <div class="main inner row fluid">
    <div class="back-logo-container"><img :src="backLogo"></div>
    <div class="row col-9">
      <div class="form row">
        <div class="form-header">Başvuru Tipi</div>
        <div class="form-body">
          <div class="input-group">
            <label for="applicationType">Başvuru Tipi</label>
            <select @change="setApplicationType" v-model="applicationType" name="applicationType" id="applicationType">
              <option value="Başvuru Tipi" selected disabled>Seçiniz</option>
              <option value="0">Bireysel</option>
              <option value="1">Kurumsal</option>
              <option value="2">Kamu</option>
            </select>
          </div>
        </div>
        <div class="form-footer"></div>
      </div>
      <div class="row">
        <div class="button-group">
          <router-link to="/"><button type="button" class="btn btn-secondary">Geri</button></router-link>
          <button @click="redirectToPage" type="button" id="btnGo" class="btn btn-primary" disabled>Devam Et</button>
        </div>
      </div>
    </div>
  </div>
</template>

<style>
</style>



