<script>
import VueNumericKeypad from 'vue-numeric-keypad'

export default {
  name: 'NumericKeypad',
  components: {
    VueNumericKeypad
  },
  data () {
    return {
      value: '',
      show: 0,
      options: {
        keyRandomize: true,
        randomizeWhenClick: false,
        fixDeleteKey: true,
        pseudoClick: false,
        pseudoClickDeleteKey: false,
        pseudoClickBlankKey: false,
        stopPropagation: true,
        vibrate: false,
        activeButtonDelay: 300,
        vibratePattern: 200,
        keyArray: [1, 2, 3, 4, 5, 6, 7, 8, 9, 0, -1, -1]
      }
    }
  },
  beforeCreate () {
    let thisVar = this

    const interval2 = setInterval(function () {
      let deleteBtns = document.getElementsByClassName('numeric-keypad__button--delete')
      let regularBtns = document.getElementsByClassName('numeric-keypad__button')

      if (deleteBtns && deleteBtns.length > 0) {
        Array.prototype.forEach.call(deleteBtns, function (deleteBtn) {
          deleteBtn.remove()
        })

        regularBtns[regularBtns.length - 1].style.marginLeft = '33.4%'

        clearInterval(interval2)
      } else if (regularBtns && regularBtns.length > 0) {
        clearInterval(interval2)
      }
    }, 10)

    document.addEventListener('DOMContentLoaded', function () {
      const interval = setInterval(function () {
        let comp = document.getElementsByClassName('enterPin')
        if (comp && comp.length > 0) {
          document.addEventListener('click', function (event) {
            if (!comp[0].contains(event.target)) {
              thisVar.show = 0
            } else {
              thisVar.show = 1

              const interval2 = setInterval(function () {
                let deleteBtns = document.getElementsByClassName('numeric-keypad__button--delete')
                let regularBtns = document.getElementsByClassName('numeric-keypad__button')

                if (deleteBtns && deleteBtns.length > 0) {
                  Array.prototype.forEach.call(deleteBtns, function (deleteBtn) {
                    deleteBtn.remove()
                  })

                  regularBtns[regularBtns.length - 1].style.marginLeft = '33.4%'

                  clearInterval(interval2)
                } else if (regularBtns && regularBtns.length > 0) {
                  clearInterval(interval2)
                }
              }, 10)
            }
          })

          clearInterval(interval)
        }

        let nums = document.getElementsByClassName('numeric-keypad__button')
        Array.prototype.forEach.call(nums, function (num) {
          num.addEventListener('click', function () {
            let comp = document.getElementsByClassName('enterPin')
            if (comp && comp.length > 0) {
              comp[0].value += num.innerHTML.trim()
              comp[0].dispatchEvent(new Event('input'))
            }
          })
        })
      }, 1000)
    })
  }
}
</script>
<template>
  <div id="app">
    <VueNumericKeypad
      :value.sync="value"
      :show.sync="show"
      :options="options"
    />
  </div>
</template>

<style scoped>
  .app {
    width: 100%;
    height: 100%
  }
</style>



