<script>
// Developed by Egemen ŞENER
import backLogo from '../assets/img/back_logo.png'
import AlertBox from '../components/AlertBox'
import info from '../assets/img/info.svg'
import Captcha from '../components/Captcha.vue'
import NumericKeypad from '../components/NumericKeypad.vue'
import SignerPin from '../components/SignerPin.vue'
import Payment from '../components/Payment.vue'
import BaseArkSignerComp from '../assets/scripts/ArkSignerComp.js'
import Datepicker from 'vuejs-datepicker'
import { HTTP } from '../helpers/services'
import {tr} from 'vuejs-datepicker/dist/locale'
import Constants from '../constants.js'

export default {
  name: 'PersonalApplicationPage',
  components: {AlertBox, Captcha, Payment, Datepicker, NumericKeypad, SignerPin},
  data () {
    return {
      Constants: Constants,
      tr: tr,
      backLogo: backLogo,
      info: info,
      signerPinProperties: {
        signerTerminals: [],
        taahutname: '',
        checkImzaliMi: false
      },
      disabledDates: {
        from: new Date(this.calc18age(), new Date().getMonth(), new Date().getDate())
      },
      currentPlan:
        {
          productType: '',
          productSku: '',
          productName: '',
          productCharge: {
            totalPrice: 0.0
          },
          childProducts: []
        },
      splitedStr: '',
      formData: {
        dealerCode: '',
        kimlikBilgileri: [
          {
            uyruk: '',
            tckn: '',
            ad: '',
            soyad: '',
            dogumYeri: '',
            dogumTarihi: new Date(this.calc18age(), new Date().getMonth(), new Date().getDate()),
            babaAdi: ''
          }
        ],
        iletisimBilgileri: [
          {
            cepTelefonu: '',
            evTelefonu: '',
            isTelefonu: '',
            il: '',
            ilce: '',
            mahalle: '',
            adres: '',
            pk: '',
            ePosta: ''
          }
        ],
        hesapBilgileri: [
          {
            hesapOid: '',
            crmOid: '',
            portalKullaniciAdi: '',
            alanAdi: Constants.ALAN_ADI,
            hesapOnAdi: '',
            baseHesapOnAdi: '',
            input: '',
            kepAdresi: '',
            hizmetTuru: Constants.HIZMET_TURU_ALMA_GONDERME
          }
        ],
        rehberBilgileri: [
          {
            rehberdeAranma: true,
            firstNamePerm: true,
            lastNamePerm: true,
            wayOfReceivingServicePerm: true,
            cityPerm: true,
            townPerm: true,
            addressPerm: true,
            phonePerm: true
          }
        ],
        paymentDetail: {
          paymentOption: '',
          paymentType: '',
          orderNumber: '',
          paidAmount: 0,
          posNumber: '',
          paymentDate: '',
          numberOfInstallments: '',
          paymentDetails: '',
          comission: 0, // user_commission
          workPlaceComission: 0 // merchant_commission
        }
      },
      alertProps: {
        message: '',
        cssClass: '',
        isActive: false
      },
      paketler: [],
      taxOff: [],
      cityWithTowns: [],
      selectedCityTowns: [],
      kimlikDogrumu: false,
      isCaptchaVerified: {
        verified: false,
        captchaCode: '',
        verifyEvent: function (callback) { callback() }
      },
      cardType: false,
      indirildiMi: false,
      taahutnameCheck1: false,
      taahutnameCheck2: false,
      odemeYapildiMi: false,
      files: [],
      secure3D: {
        invoice_id: '',
        hash_key: ''
      }
    }
  },
  methods: {
    getArkSignerObj (cb) {
      let baseArkSignerComp = new BaseArkSignerComp()
      let arkSignerComp = new baseArkSignerComp.ArkSignerComp()
      let mainObj = Object.assign(arkSignerComp, JSON.parse(window.localStorage.getItem('arkSignerObj')))
      mainObj.construct(mainObj)
      cb(mainObj)
    },
    async getTaahutname () {
      const thiz = this
      const veriTaahutname = JSON.stringify({
        uyruk: this.formData.kimlikBilgileri[0].uyruk,
        tckn: this.formData.kimlikBilgileri[0].tckn,
        adSoyad: this.formData.kimlikBilgileri[0].ad + ' ' + this.formData.kimlikBilgileri[0].soyad,
        babaAdi: this.formData.kimlikBilgileri[0].babaAdi,
        dogumTarihi: this.changeFormat(this.formData.kimlikBilgileri[0].dogumTarihi),
        dogumYeri: this.formData.kimlikBilgileri[0].dogumYeri,
        mobilTelefon: this.formData.iletisimBilgileri[0].cepTelefonu,
        eposta: this.formData.iletisimBilgileri[0].ePosta,
        faturaAdresi: this.formData.iletisimBilgileri[0].adres,
        postaKodu: this.formData.iletisimBilgileri[0].pk,
        ilce: this.formData.iletisimBilgileri[0].ilce,
        il: this.formData.iletisimBilgileri[0].il,
        hesapTipi: this.formData.hesapBilgileri[0].hizmetTuru,
        kepAdresi: this.formData.hesapBilgileri[0].kepAdresi,
        paketSecimi: this.currentPlan.productName,
        tarifeSecimi: this.currentPlan.productDesc,
        odemeSekli: this.formData.paymentDetail.paymentType
      })
      const response = await HTTP.post('getTaahutname', {display: false, formParameters: veriTaahutname, basvuruTipi: 'Bireysel'}, {headers: {'Content-Type': 'application/json'}}).catch(e => {
      })
      if (this.signerPinProperties.checkImzaliMi !== true) {
        this.signerPinProperties.taahutname = response.data.data
        this.generateFile(this.signerPinProperties.taahutname, 'BIREYSEL_KEP_BASVURU.pdf')
      } else {
        this.generateFile(this.signerPinProperties.taahutname, 'IMZALI_KEP_BASVURU.pdf')
      }
      this.indirildiMi = true
      if (localStorage.getItem('Kart Tipi') === 'true') {
        Array.prototype.forEach.call(Object.keys(window.arkSignerComp.getTerminals()), function (key) {
          let cardInfo = window.arkSignerComp.getTerminals()[key]
          let certInfo = thiz.decodeBase64StrAsUTF8(window.arkSignerComp.getCertificates()[Object.keys(window.arkSignerComp.getCertificates())[0]].subject)
          thiz.signerPinProperties.signerTerminals.push({'card': atob(cardInfo.terminal), 'cert': certInfo})
        })
      }
    },
    async bireyselHesapVePortalAdiUret (ad, soyad) {
      // Yeniden üretmek için clear ediyoruz
      this.formData.hesapBilgileri[0].baseHesapOnAdi = ''
      this.formData.hesapBilgileri[0].hesapOnAdi = ''
      this.formData.hesapBilgileri[0].portalKullaniciAdi = ''
      this.formData.hesapBilgileri[0].kepAdresi = ''
      this.formData.hesapBilgileri[0].input = ''

      let requestParams = {ad: ad, soyad: soyad}

      if ((ad + soyad).length > 25) {
        requestParams.ad = ad.slice(0, 12)
        requestParams.soyad = soyad.slice(0, 13)
      }

      const response = await HTTP.post('HesapOnAdiOner', requestParams)
      let hesapOnAdi = response.data.data[0]

      const response1 = await HTTP.post('KepIsExistContactOnPortal', {request: {portalUserName: hesapOnAdi}})
      let portalFlag = response1.data.IsExist

      if (hesapOnAdi.substr(hesapOnAdi.length - 1) !== '.' && portalFlag) {
        this.formData.hesapBilgileri[0].baseHesapOnAdi = hesapOnAdi + '.'
      } else {
        this.formData.hesapBilgileri[0].baseHesapOnAdi = hesapOnAdi
      }

      this.formData.hesapBilgileri[0].hesapOnAdi = this.formData.hesapBilgileri[0].baseHesapOnAdi
      this.formData.hesapBilgileri[0].portalKullaniciAdi = hesapOnAdi
      this.formData.hesapBilgileri[0].kepAdresi = this.formData.hesapBilgileri[0].hesapOnAdi + '@' + this.formData.hesapBilgileri[0].alanAdi
    },
    async bireyselHesapVePortalAdiKontrolEt () {
      let input = this.formData.hesapBilgileri[0].input
      let baseHesapOnAdi = this.formData.hesapBilgileri[0].baseHesapOnAdi
      let sonCharacter = baseHesapOnAdi.substr(baseHesapOnAdi.length - 1)
      if (input !== '' && this.firstDigit(input) <= 0) {
        this.scrollToTop()
        this.alertProps.isActive = true
        this.alertProps.message = '0 veya daha düşük bir sayı balangıç değeri girilemez!'
        this.alertProps.cssClass = 'alert alert-danger mt-3'
        this.hesapVePortalAdiUygunMu = false
        return false
      }
      if (input === '' && sonCharacter === '.') {
        this.scrollToTop()
        this.alertProps.isActive = true
        this.alertProps.message = 'Hesap Adı alınmış lütfen ek bir değer giriniz!'
        this.alertProps.cssClass = 'alert alert-danger mt-3'
        this.hesapVePortalAdiUygunMu = false
        return false
      }
      this.formData.hesapBilgileri[0].portalKullaniciAdi = ''
      this.formData.hesapBilgileri[0].kepAdresi = ''
      if (sonCharacter !== '.' && input !== '') {
        this.formData.hesapBilgileri[0].baseHesapOnAdi = baseHesapOnAdi + '.'
      }
      let hesapOnAdi = this.formData.hesapBilgileri[0].baseHesapOnAdi + input
      let hesapOnAdiFlag = await HTTP.post('HesapOnAdiMusaitMi', {hesapOnAdi: hesapOnAdi})
      const response1 = await HTTP.post('KepIsExistContactOnPortal', {request: {portalUserName: hesapOnAdi}})
      let portalFlag = response1.data.IsExist
      if (hesapOnAdiFlag && !portalFlag) {
        this.formData.hesapBilgileri[0].hesapOnAdi = hesapOnAdi
        this.formData.hesapBilgileri[0].portalKullaniciAdi = hesapOnAdi
        this.formData.hesapBilgileri[0].kepAdresi = this.formData.hesapBilgileri[0].hesapOnAdi + '@' + this.formData.hesapBilgileri[0].alanAdi
        this.hesapVePortalAdiUygunMu = true
        return true
      } else {
        this.scrollToTop()
        this.alertProps.isActive = true
        this.alertProps.message = 'Hesap Adı alınmış lütfen ek bir değer giriniz!'
        this.alertProps.cssClass = 'alert alert-danger mt-3'
        this.hesapVePortalAdiUygunMu = false
        return false
      }
    },
    async bireyselHesapAcma () {
      this.formData.hesapBilgileri[0].crmOid = this.generateCrmOid() // Crm oid'yi üretiyoruz
      var data = {bireyselBasvuru: JSON.stringify({
        hesapOnAdi: this.formData.hesapBilgileri[0].hesapOnAdi,
        subdomain: '', // normal için boş kalacak
        ad: this.formData.kimlikBilgileri[0].ad,
        soyad: this.formData.kimlikBilgileri[0].soyad,
        tcKimlikNo: this.formData.kimlikBilgileri[0].tckn,
        cepTelefonu: this.formData.iletisimBilgileri[0].cepTelefonu,
        email: this.formData.iletisimBilgileri[0].ePosta,
        hizmetTuru: this.formData.hesapBilgileri[0].hizmetTuru === Constants.HIZMET_TURU_ALMA ? '1' : '2',
        babaAdi: this.formData.kimlikBilgileri[0].babaAdi,
        dogumYeri: this.formData.kimlikBilgileri[0].dogumYeri,
        kullaniciKodu: this.formData.hesapBilgileri[0].portalKullaniciAdi,
        subdomainVarMi: 0, // Ekstra bir subdomain eklenip eklenmeyeceği bilgisini verir
	        dogumTarihi: this.changeFormat(this.formData.kimlikBilgileri[0].dogumTarihi), // Dogum tarihi bilgisinde sorun var 1 ay eksik alıyor
	        uyruk: this.formData.kimlikBilgileri[0].uyruk === Constants.UYRUK_TURK_VATANDASI ? '1' : '0',
	        tebligatAdresi: 0,
        checkboxAd: this.formData.rehberBilgileri[0].firstNamePerm === false ? 0 : 1,
        checkboxAdres: this.formData.rehberBilgileri[0].addressPerm === false ? 0 : 1,
        checkboxHizmetalmasekli: this.formData.rehberBilgileri[0].wayOfReceivingServicePerm === false ? 0 : 1,
        checkboxIl: this.formData.rehberBilgileri[0].cityPerm === false ? 0 : 1,
        checkboxIlce: this.formData.rehberBilgileri[0].townPerm === false ? 0 : 1,
        checkboxSoyad: this.formData.rehberBilgileri[0].lastNamePerm === false ? 0 : 1,
        checkboxTelefonNumarasi: this.formData.rehberBilgileri[0].phonePerm === false ? 0 : 1
      }),
      basvuruAnaEkran: JSON.stringify({
        ilceCmb: this.formData.iletisimBilgileri[0].ilce,
        ilCmb: this.selectedCityTowns.city,
        adres: this.formData.iletisimBilgileri[0].adres
      }),
      crmOid: this.formData.hesapBilgileri[0].crmOid // Crm oid'yi hesapOid olarak crm'e atıyoruz
      }
      console.log(data)
      const response = await HTTP.post('BireyselHesapAcma', data)
      console.log(response)
      if (response.data.hesapOid) {
        this.formData.hesapBilgileri[0].hesapOid = response.data.hesapOid // Kep'den dönen hesapOid'yi setliyoruz
        this.formData.hesapBilgileri[0].kepAdresi = response.data.kepHesabiAdresi // Kep'den dönen kep hesabı adresini setliyoruz
        return true
      }
      this.scrollToTop()
      this.alertProps.isActive = true
      this.alertProps.message = response.data
      this.alertProps.cssClass = 'alert alert-danger mt-3'
      return false
    },
    async bireyselBasvuruGonder () {
      if (await !this.checkTaahutnameProcess()) {
        return false
      }
      await this.bireyselHesapAcma()
      let kepApplicationRequest = { kepApplicationRequest: {
        applicationType: 'Bireysel',
        isPreApplication: false,
        generalInfo: {
          nationality: this.formData.kimlikBilgileri[0].uyruk,
          tcknYKN: this.formData.kimlikBilgileri[0].tckn,
          dealerCode: this.formData.dealerCode,
          firstName: this.formData.kimlikBilgileri[0].ad,
          lastName: this.formData.kimlikBilgileri[0].soyad,
          birthPlace: this.formData.kimlikBilgileri[0].dogumYeri,
          birthDate: this.changeFormat(this.formData.kimlikBilgileri[0].dogumTarihi),
          fatherName: this.formData.kimlikBilgileri[0].babaAdi,
          mobile: this.formData.iletisimBilgileri[0].cepTelefonu,
          homePhone: this.formData.iletisimBilgileri[0].evTelefonu,
          officePhone: this.formData.iletisimBilgileri[0].isTelefonu,
          city: this.formData.iletisimBilgileri[0].il,
          town: this.formData.iletisimBilgileri[0].ilce,
          neighbourhood: this.formData.iletisimBilgileri[0].mahalle,
          address: this.formData.iletisimBilgileri[0].adres,
          postalCode: this.formData.iletisimBilgileri[0].pk,
          email: this.formData.iletisimBilgileri[0].ePosta
        },
        accountInfo: {
          accountOid: this.formData.hesapBilgileri[0].hesapOid,
          crmOid: this.formData.hesapBilgileri[0].crmOid,
          portalUserName: this.formData.hesapBilgileri[0].portalKullaniciAdi,
          accountPreName: this.formData.hesapBilgileri[0].hesapOnAdi,
          domainName: this.formData.hesapBilgileri[0].alanAdi,
          kepAddress: this.formData.hesapBilgileri[0].kepAdresi,
          serviceType: this.formData.hesapBilgileri[0].hizmetTuru
        },
        guideInfo: {
          searchPermissionInDirectory: this.formData.rehberBilgileri[0].rehberdeAranma,
          firstNamePerm: this.formData.rehberBilgileri[0].firstNamePerm,
          lastNamePerm: this.formData.rehberBilgileri[0].lastNamePerm,
          wayOfReceivingServicePerm: this.formData.rehberBilgileri[0].wayOfReceivingServicePerm,
          cityPerm: this.formData.rehberBilgileri[0].cityPerm,
          townPerm: this.formData.rehberBilgileri[0].townPerm,
          addressPerm: this.formData.rehberBilgileri[0].addressPerm,
          phonePerm: this.formData.rehberBilgileri[0].phonePerm
        },
        productDetail: {
          productCode: this.currentPlan.productSku,
          channel: ''
        },
        paymentDetail: this.formData.paymentDetail,
        contractDetail: {
          contractType: localStorage.getItem('Kart Tipi') === 'false' ? 'Islak İmza' : 'e-İmza',
          contractVersion: 'DD110',
          contractFile: this.signerPinProperties.taahutname
        } }
      }
      console.log(kepApplicationRequest)
      await this.basvuruGonder(kepApplicationRequest, 'BireyselBasvuru')
    },
    printPaymentDetail: function () {
      console.log(this.formData.paymentDetail)
    }
  },
  mounted () {
    const thiz = this
    thiz.getPakets('BireyselProducts')
    thiz.getCityTownTaxOffice()
    thiz.disableInput()
    if (localStorage.getItem('Kart Tipi') === 'true') {
      this.getArkSignerObj(function (obj) {
        window.arkSignerComp = obj
        if (window.arkSignerComp.getCertificates()[Object.keys(window.arkSignerComp.getCertificates())[0]].subjectSerialNumber !== undefined) {
          thiz.formData.kimlikBilgileri[0].tckn = thiz.decodeBase64StrAsUTF8(window.arkSignerComp.getCertificates()[Object.keys(window.arkSignerComp.getCertificates())[0]].subjectSerialNumber)
          thiz.splitedStr = thiz.decodeBase64StrAsUTF8(window.arkSignerComp.getCertificates()[Object.keys(window.arkSignerComp.getCertificates())[0]].subjectCommonName).split(' ')
          thiz.formData.kimlikBilgileri[0].ad = thiz.splitedStr[0]
          thiz.formData.kimlikBilgileri[0].soyad = thiz.splitedStr[1]
        }
      })
      let btnImzala = document.getElementById('taahutnameImzala')
      let info = document.getElementById('info')
      btnImzala.removeAttribute('hidden')
      info.removeAttribute('hidden')
      this.bireyselHesapVePortalAdiUret(this.formData.kimlikBilgileri[0].ad, this.formData.kimlikBilgileri[0].soyad)
    }
    this.otoFillData(0)
  }
}
</script>

<template>
  <div class="col-xl-8 col-lg-10 col-md-12 col-sm-12 col-xs-12 wizard-container m-auto">
    <div class="back-logo-container"><img :src="backLogo" /></div>
    <form-wizard ref="wizard" shape="tab" color="#870052" back-button-text="Geri" next-button-text="Devam Et"
      finish-button-text="Başvuruyu Tamamla">
     <div id ="finansLoader" v-bind:hidden="isLoading ? false : true" style="opacity: 80%; pointer-events: none;"><img :src="progressLogo"/>Lütfen Bekleyiniz!</div>
      <p class="text-left" slot="title">
        Aşağıdaki formu eksiksiz doldurarak <strong>"Devam Et"</strong> butonuna
        tıklayınız.
      </p>
      <tab-content title="01. Ürün Seçimi" :before-change="checkCaptchaPackage">
        <div class="row col-12 csc-card pt-4">
          <div class="row csc-card-title border-bottom hor-row">
            <div class="col-6 p-0">
              <p class="main-head blue">Ürün Seçimi</p>
            </div>
            <div class="col-6 p-0">
              <p class="sub-head text-right">
              </p>
            </div>
          </div>
          <div class="csc-card-body">
            <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps"/>
            <p class="sub-head pt-3 pb-3">Size uygun paket seçin.</p>
            <div class="d-flex justify-content-between" style="flex-wrap: wrap;">
              <div v-for="paket in this.paketler" :key="paket.productSku" v-bind:id="paket.productSku"
                class="card col-xl-4 col-lg-4 col-md-4 col-sm-12 col-xs-12 pt-4 pb-4">
                <div class="cards">
                  <div class="card-title border-bottom">
                    <h4 class="main-head blue">{{ paket.productName }}</h4>
                    <h6 class="main-head blue d-flex space-between">
                      <p>{{ paket.productDesc }}</p>
                      <p class="main-head red"><strong>{{ paket.productCharge.totalPrice }} ₺</strong></p>
                    </h6>
                  </div>
                  <div class="card-body">
                    <ul class="feature-list">
                      <li v-for="included in paket.childProducts" v-bind:key="included.childProductDesc"
                        class="d-block">
                        <span>
                          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M6 12L10.2857 16L18 8" stroke="#870052" stroke-width="2" stroke-miterlimit="10" />
                          </svg>
                        </span>
                        <span>{{ included.childProductDesc }}</span>
                      </li>
                    </ul>
                  </div>
                  <div class="card-footer p-0 pb-2 borderless transparent">
                    <div class="button-group p-0 align-center">
                      <button @click="chooseCard(`${paket.productSku}`, paket)" type="button"
                        class="btn btn-primary light w-100">Paketi Seç</button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="d-flex form p-0 mt-3 bayiFormArea">
              <div class="form-body p-0">
                <div class="input-group pt-4 pb-4">
                  <label for="bayiKodu">Bayi kodunu giriniz</label>
                  <img :src="info" width="35px" height="35px" title="Güncel bayi kodunuzu bu alana giriniz"/>
                  <input type="text" name="bayiKodu" id="bayiKodu" placeholder="Bayi Kodu" v-model="formData.dealerCode">
                </div>
                <Captcha :isCaptchaVerified.sync="isCaptchaVerified"></Captcha>
              </div>
            </div>
          </div>
          <div class="csc-card-footer"></div>
        </div>
      </tab-content>
      <tab-content title="02. Genel Bilgiler" :before-change="checkFailureClass">
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">1/4</p>
            <p class="main-head blue">Kimlik Bilgileri</p>
            <span @click="collapsePanel('kimlikBilgileri')"
              class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps"/>
          <div class="acc-body" id="kimlikBilgileri">
            <div class="form">
              <div class="form-body">
                <div class="input-group">
                  <label for="uyruk">Uyruk</label>
                  <select @onChange="checkValidation()" @focus="focusElement()" name="uyruk" id="uyruk" v-model="formData.kimlikBilgileri[0].uyruk">
                    <option value="Uyruk" selected disabled>Uyruk</option>
                    <option :value="Constants.UYRUK_TURK_VATANDASI">Türk Vatandaşı</option>
                    <option :value="Constants.UYRUK_YABANCI">Yabancı</option>
                  </select>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="tckn">TCKN/Yabancı Kimlik No</label>
                  <input @blur="checkValidation(), kimlikDogrula()" @focus="focusElement()" type="number" name="tckn" id="tckn" v-model="formData.kimlikBilgileri[0].tckn" :disabled=cardType />
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="ad">Ad</label>
                  <input @blur="checkValidation()" @focus="focusElement()" type="text" name="ad" id="ad" v-model="formData.kimlikBilgileri[0].ad" :disabled=cardType>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="soyad">Soyad</label>
                  <input @blur="checkValidation(), bireyselHesapVePortalAdiUret(formData.kimlikBilgileri[0].ad, formData.kimlikBilgileri[0].soyad)" @focus="focusElement()" type="text" name="soyad" id="soyad" v-model="formData.kimlikBilgileri[0].soyad" :disabled=cardType>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="dogumYeri">Doğum Yeri</label>
                  <input @blur="checkValidation()" @focus="focusElement()" type="text" name="dogumYeri" id="dogumYeri" v-model="formData.kimlikBilgileri[0].dogumYeri">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="dogumTarihi">Doğum Tarihi</label>
                  <datepicker :language="tr" v-model="formData.kimlikBilgileri[0].dogumTarihi" format="dd/MM/yyyy" :disabled-dates="disabledDates" @blur="checkValidation()" @focus="focusElement()"></datepicker>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="babaAdi">Baba Adı</label>
                  <input @blur="checkValidation()" @focus="focusElement()" type="text" name="babaAdi" id="babaAdi" v-model="formData.kimlikBilgileri[0].babaAdi">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">2/4</p>
            <p class="main-head blue">İletişim Bilgileri</p>
            <span @click="collapsePanel('iletisimBilgileri')"
              class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <div class="acc-body" id="iletisimBilgileri">
            <div class="form">
              <div class="form-body">
                <div class="input-group">
                  <label for="cep">Cep Telefonu</label>
                  <input @blur="checkValidation(), phoneControl(false)" @keypress="filter(event)" @focus="focusElement()" type="number" name="cep" id="cep" v-model="formData.iletisimBilgileri[0].cepTelefonu">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="ev">Ev Telefonu (Opsiyonel)</label>
                  <input type="number" @keypress="filter(event)" name="ev" id="ev" v-model="formData.iletisimBilgileri[0].evTelefonu">
                </div>
                <div class="input-group">
                  <label for="is">İş Telefonu (Opsiyonel)</label>
                  <input type="number" @keypress="filter(event)" name="is" id="is" v-model="formData.iletisimBilgileri[0].isTelefonu">
                </div>
                <div class="input-group">
                  <label for="il">İl</label>
                  <select @change="checkValidation(), setCity()" @focus="focusElement()" v-model="selectedCityTowns" name="il" id="il">
                    <option value="İl" selected disabled>İl</option>
                    <option v-for="cityInfo in this.cityWithTowns" :key="cityInfo.city" :value="cityInfo">
                      {{ cityInfo.city }}</option>
                  </select>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="ilce">İlçe</label>
                  <select @change="checkValidation()" @focus="focusElement()" name="ilce" id="ilce" v-model="formData.iletisimBilgileri[0].ilce">
                    <option value="ilce" selected disabled>Seçiniz</option>
                    <option v-for="towns in selectedCityTowns.townsOfCity" :key="towns.city">{{ towns }}</option>
                  </select>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="mahalle">Mahalle</label>
                  <input type="text" name="mahalle" id="mahalle" v-model="formData.iletisimBilgileri[0].mahalle"/>
                </div>
                <div class="input-group">
                  <label for="adres">Adres</label>
                  <textarea @blur="checkValidation()" @focus="focusElement()" name="adres" id="adres" v-model="formData.iletisimBilgileri[0].adres"></textarea>
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="pk">Posta Kodu</label>
                  <input @blur="postCodeControl(), checkValidation()" @focus="focusElement()" type="number" name="pk" id="pk" v-model="formData.iletisimBilgileri[0].pk">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
                <div class="input-group">
                  <label for="eposta">e-Posta</label>
                  <input @blur="emailControl(false), checkValidation()" @focus="focusElement()" type="email" name="eposta" id="eposta" v-model="formData.iletisimBilgileri[0].ePosta">
                  <div class="mustArea" style="visibility: hidden"><p>Bu alan gerekli</p></div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">3/4</p>
            <p class="main-head blue">Hesap Bilgileri</p>
            <span @click="collapsePanel('hesapBilgileri')"
              class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <div class="acc-body hesapBilgileriAlan" id="hesapBilgileri">
            <div class="form">
              <div class="form-body">
                <div class="input-group">
                  <label for="pka">Portal Kullanıcı Adı<img :src="info" width="35px" height="35px" style="margin-left: 3%;" title="Hesabınza girerken kullanacağınız kullanıcı adını seçiniz."/></label>
                  <input @blur="checkValidation()"  disabled v-model="formData.hesapBilgileri[0].portalKullaniciAdi" name="pka" id="pka">
                </div>
                <div class="input-group">
                  <label for="alanAdi">Alan Adı</label>
                  <input @blur="checkValidation()" type="text" name="alanAdi" id="alanAdi" disabled v-model="formData.hesapBilgileri[0].alanAdi">
                </div>
                <div class="input-group">
                  <label for="hoa" >Hesap Ön Adı<img :src="info" width="35px" height="35px" style="margin-left: 3%;" title="BTK tarafından belirlenen standartlar doğrultusunda oluşturulan KEP adresini seçiniz."/></label>
                  <input class="col" disabled v-model="formData.hesapBilgileri[0].baseHesapOnAdi" />
                  <input name="hoa" @blur="bireyselHesapVePortalAdiKontrolEt()" class="col-2" v-model="formData.hesapBilgileri[0].input" type="number"/>
                </div>
                <div class="input-group">
                  <label for="kepAdresi">Kep Adresi</label>
                  <input @blur="checkValidation()" type="text" name="kepAdresi" id="kepAdresi" disabled v-model="formData.hesapBilgileri[0].kepAdresi">
                </div>
                <div class="input-group">
                  <label for="hizmetTuru">Hizmet Türü</label>
                  <select name="hizmetTuru" id="hizmetTuru" v-model="formData.hesapBilgileri[0].hizmetTuru">
                    <option :value="Constants.HIZMET_TURU_ALMA">Alma</option>
                    <option :value="Constants.HIZMET_TURU_ALMA_GONDERME">Alma/Gönderme</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head red">4/4</p>
            <p class="main-head blue">Rehber Bilgileri</p>
            <span @click="collapsePanel('rehberBilgileri')"
              class="position-absolute r-0 with-border w24 vertical-centered">
              <font-awesome-icon icon="fa-solid fa-chevron-up" />
            </span>
          </div>
          <div class="acc-body" id="rehberBilgileri">
            <div class="form">
              <div class="form-body">
                <div class="input-group radio-container mb-4">
                  <label>Rehberde Aranma</label>
                  <div class="mr-4">
                    <input @change="controlRehberPermissions(true)" type="radio" name="izin" id="evet" v-model="formData.rehberBilgileri[0].rehberdeAranma" v-bind:value="true">
                    <label for="evet">İzin veriyorum</label>
                  </div>
                  <div>
                    <input @change="controlRehberPermissions(false)" type="radio" name="izin" id="hayir" v-model="formData.rehberBilgileri[0].rehberdeAranma" v-bind:value="false">
                    <label for="hayir">İzin vermiyorum</label>
                  </div>
                </div>
                <div class=" checkbox-container" v-bind:hidden="formData.rehberBilgileri[0].rehberdeAranma ? false : true">
                  <label>Rehberde sorgulanmasını istediğiniz alanlar</label>
                  <div class="checkboxes-container">
                    <label for="isim" class="option"><p class="checkboxText">Ad</p>
                      <input type="checkbox" aria-checked="false" name="isim" id="isim" v-model="formData.rehberBilgileri[0].firstNamePerm"/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="soyisim" class="option"><p class="checkboxText">Soyad</p>
                      <input type="checkbox" id="soyisim" name="soyisim" aria-checked="false" v-model="formData.rehberBilgileri[0].lastNamePerm" />
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="has" class="option"><p class="checkboxText">Hizmet alma şekli</p>
                      <input type="checkbox" id="has" name="has" aria-checked="false"  v-model="formData.rehberBilgileri[0].wayOfReceivingServicePerm"/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="sehir" class="option"><p class="checkboxText">İl</p>
                      <input type="checkbox" id="sehir" name="sehir" aria-checked="false" v-model="formData.rehberBilgileri[0].cityPerm"/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="kaza" class="option"><p class="checkboxText">İlçe</p>
                      <input type="checkbox" id="kaza" name="kaza" aria-checked="false"  v-model="formData.rehberBilgileri[0].townPerm"/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="adrs" class="option"><p class="checkboxText">Adres</p>
                      <input type="checkbox" id="adrs" name="adrs" aria-checked="false" v-model="formData.rehberBilgileri[0].addressPerm"/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                    <label for="telNo" class="option"><p class="checkboxText">Telefon Numarası</p>
                      <input type="checkbox" id="telNo" name="telNo" aria-checked="false"  v-model="formData.rehberBilgileri[0].phonePerm"/>
                      <span class="checkbox checkbox3"></span>
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </tab-content>
      <tab-content title="03. Ödeme" :before-change="validateHashKey">
        <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps"/>
        <Payment :currentPlan="currentPlan" :kimlikBilgileri="formData.kimlikBilgileri[0]" :secure3D.sync="secure3D" :alertProps="alertProps" :paymentDetail.sync ="formData.paymentDetail" :parentRef="$refs" :odemeYapildiMiParent="odemeYapildiMi"></Payment>
      </tab-content>
      <tab-content title="04. Taahhütname" :before-change="bireyselBasvuruGonder">
        <AlertBox v-if="this.alertProps.isActive" :alert-props="this.alertProps"/>
        <NumericKeypad></NumericKeypad>
        <SignerPin :signerPinProp.sync="signerPinProperties" id="signerPin" hidden/>
        <div class="form">
          <label for="info" id="info" hidden>Taahhütname indirildikten sonra imzalama aşamasına geçilecektir!</label>
          <div class="form-body">
            <span class="mr-4" style="margin-left: 3%;">
                <button style="all:unset" @click="getTaahutname()">
                <font-awesome-icon class="mr-2" icon="fa-solid fa-file-lines" />Taahhütnameyi indir
                <span>
                  <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M17 11L12 16L7 11" stroke="#870052" stroke-width="2" stroke-miterlimit="10" />
                    <path d="M12 16L12 4" stroke="#870052" stroke-width="2" stroke-miterlimit="10" />
                    <path d="M4 20H20" stroke="#870052" stroke-width="2" stroke-miterlimit="10" />
                  </svg>
                </span>
              </button>
              </span>
              <span id="taahutnameImzala" @click="taahutnameImzala()" hidden>
                <button style="all:unset" v-bind:hidden="indirildiMi ? false : true">
                <font-awesome-icon class="mr-2" icon="fa-solid fa-file-lines" />Taahhütnameyi İmzala
                </button>
              </span>
              <div class="taahhut-text" style="margin-top: 1%;">
              <input type="checkbox" name="onayladim" id="onayladim" v-model="taahutnameCheck1">
              <label for="onayladim">Taahhütnameyi okudum ve hesabımın açılmasını onaylıyorum.</label>
            </div>
            <div class="taahhut-text">
              <input type="checkbox" name="saklaİlet" id="saklaİlet" v-model="taahutnameCheck2">
              <label for="saklaİlet">Sakla-İlet yöntemi ile KEP hizmeti almayı kabul ediyorum.</label>
            </div>
            <div class="input-group long-label">
              <h6 class="font-color red" style="margin-left: 5%; margin-top: 5%;">TALEP EDİLEN BELGELER</h6>
                <ul class="listed-item" style="margin-left: 5%;">
                  <li> Nüfus cüzdanı, pasaport, sürücü belgesi gibi fotoğraflı ve kimlik yerine geçen geçerli resmi belgenin fotokopisi</li>
                  <li> Bireysel Kayıtlı Elektronik Posta (KEP) Hizmet Taahhütnamesi’nin imzalı hali</li>
                </ul>
                <div class="form-body">
                <p class="info"><img :src="info" width="33px" height="33px"/> İşlemlerinizin tamamlanabilmesi için talep edilen belgelerinizin ıslak imzalı halinin firmamıza kargolanması gerekmektedir.</p>
                <div class="kargo">
                <p style="font-weight: 500;">Kargo Bilgileri;</p>
                <p>Alıcı adı:     eFinans Elektronik Ticaret ve Bilişim Hizmetleri A.Ş.</p>
                <p>Adres   :     Esentepe Mahallesi Büyükdere Caddesi QNB Finansbank Kristal Kule Binası No:215 Şişli 34394 İstanbul</p>
                <p>İletişim :    0212 370 42 00</p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="acc-container">
          <div class="acc-header position-relative border-bottom pb-4">
            <p class="main-head blue">EK DOSYA YÜKLEME</p>
          </div>
          <div class="acc-body">
            <div class="form p-0">
              <div class="form-body p-0 pt-2">
                <div class="file-upload input-group mt-4 position-relative">
                  <input class="position-relative"  type="file" name="files" id="files" ref="files" multiple v-on:change="handleFileUpload()">
                  <div class="file-upload-cover position-absolute">
                    <p>
                      <span>
                        <font-awesome-icon class="fa-3x" icon="fa-solid fa-cloud-arrow-up" />
                      </span>
                    </p>
                    <p>Yüklemek istediğiniz dosyayı sürükleyin ya da <strong class="font-color blue">seçin</strong></p>
                    <p class="font-color xs">Desteklenen dosya formatı ".zip"</p>
                  </div>
                </div>
                </div>
                <div v-for="(file, index) in this.files" :key="index">
                <div class="d-flex index">
                  <p class="col-9 p-2 mt-2"> {{ index + 1 }}<span class="dosya">{{ ". Yüklenen Dosya " +
                    file.name }}</span></p>
                  <button class="btn btn-primary btn-sm mt-2 mr-4" type="button" v-on:click="removeFile(index)">Dosyayı Kaldır</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </tab-content>
    </form-wizard>
  </div>
</template>

<style scoped>
.wizard-container {
  padding: 118px 96px 96px 96px;
  height: 100vh;
  box-sizing: border-box;
}
</style>



