<script>
/* eslint-disable */
import backLogo from '../assets/img/back_logo.png'
import SuccessModal from '../components/SuccessModal.vue'
import BaseArkSignerComp from '../assets/scripts/ArkSignerComp.js'
import { utils } from '../assets/scripts/utils.js'
export default {
  name: "VerifyPage",
  components: { SuccessModal },
  data() {
    return {
      backLogo: backLogo,
      isModalOpen: false,
      succesModalProperties: {
        applicationType: ''
      }
    };
  },
  methods: {
    initArkSigner: function (cb) {
      let baseArkSignerComp = new BaseArkSignerComp()
      window.arkSignerComp = new baseArkSignerComp.ArkSignerComp()

      let args = {
        okay: true,
        header: 'Mali Mühür/E-İmza İmzalama Aracı',
        body: 'İmza client uygulaması açılıyor..',
        buttonCount: 1,
        info: true
      }
      utils.toggleMessagePopup(args)

      utils.initializeResizing()

      window.arkSignerComp.init(function (result) {
        if (result) {
          window.localStorage.setItem('arkSignerUrl', 'https://portal.efinans.com.tr/yonetim')
          window.arkSignerComp.setRequestUrl('https://portaltest.efinans.com.tr/signer/ark.signer.pades/view/pades')
          window.arkSignerComp.setSourceUrl('http://test.e-defteryukleme.com/edefterent/download?cmd=DOWNLOAD_ARKSIGNER_CLIENT&')
          window.arkSignerComp.setIdVerify(false)
          window.arkSignerComp.setIdentityNumber('')
          window.arkSignerComp.setFullyLoaded(true)
          window.arkSignerComp.setVersion('3.0.1')

          window.arkSignerComp.start(function (arkSignerDownloadUrl) {
            cb(undefined, { result: true, signerUrl: arkSignerDownloadUrl })
          })
        } else {
          cb(undefined, { result: false, signerUrl: '' })
        }
      })
    }
  },
  mounted () {
    this.initArkSigner(function (_, resp) {
      if (resp.result) {
        console.log('ArkSigner component is initialized.')
        let args
        if (window.arkSignerComp.terminalName) {
          if (window.arkSignerComp.certificates && Object.keys(window.arkSignerComp.certificates).length > 0) {
            args = {
              okay: true,
              header: 'Mali Mühür/E-İmza İmzalama Aracı',
              body: 'Tebrikler! İmza validasyonu başarılı.',
              buttonCount: 1,
              headerImg: backLogo,
              success: true
            }
            window.localStorage.setItem("arkSignerObj", JSON.stringify(window.arkSignerComp))
          } else {
            args = {
              okay: true,
              header: 'Mali Mühür/E-İmza İmzalama Aracı',
              body: 'Takılı kart bulundu. Sertifika bulunamadı.',
              buttonCount: 1,
              warning: true,
              callback: function() {}
            }
          }
          utils.toggleMessagePopup(args)
        } else if (!resp.signerUrl) {
          args = {
            okay: true,
            header: 'Mali Mühür/E-İmza İmzalama Aracı',
            body: 'Takılı imzalama aracı bulunamadı! Lütfen imza aracının takılı olduğundan emin olunuz!',
            buttonCount: 1,
            error: true,
            callback: function() {}
          }
          utils.toggleMessagePopup(args)
        } else {
          document.getElementById("signer-download-button").href = resp.signerUrl;
        }
      } else {
        console.error('ArkSigner component cannot be initialized.')
      }
    }),
   this.succesModalProperties.applicationType = localStorage.getItem('Basvuru Tipi')
  }
}
</script>

<template>
    <div class="main inner row fluid">
      <div class="back-logo-container"><img :src="backLogo"></div>
      <div id="message-popup-pile"></div>
      <div class="row col-9 bg-light">
       <div class="row col-12 border-bottom pl-0 pr-0 pt-4 pb-4 mb-4">
        <h4 class="main-head blue">e-İmza İle Doğrulama</h4>
        <p>e-İmza imzalama aracı bulunamamıştır. İmzalama işlemine devam edebilmeniz için uygulamayı bilgisayarınıza indirip kurmanız gerekmektedir.</p>
        <p><a target="_blank" id="signer-download-button" href="#">Uygulamayı İndir</a><span class="font-color red ml-2"><font-awesome-icon icon="fa-solid fa-download" /></span></p>
       </div>
       <div class="row col-12">
        <p>
            <span class="font-color red"><font-awesome-icon icon="fa-solid fa-circle-info" /></span>
            <span class="font-color gray size-m">Başvurunuza devam edebilmeniz için e-İmza'nız bilgisayarınıza takılı olmalıdır.</span>
        </p>
       </div>
      </div>
      <!--<SuccessModal :succes-modal-properties="succesModalProperties" />-->
    </div>
</template>

<style>
@import url("../assets/css/ui.css");
</style>



