<script>
import { v4 as uuidv4 } from 'uuid'
import { HTTP } from '../helpers/services'
export default {
  name: 'Captcha',
  props: ['isCaptchaVerified'],
  data () {
    return {
      uniqueID: '',
      captchaLink: '',
      captchaCode: '',
      isVerified: {
        verified: this.isCaptchaVerified.verified,
        captchaCode: this.isCaptchaVerified.captchaCode,
        verifyEvent: function (callback) { callback() }
      }
    }
  },
  methods: {
    getUniqueId: function () {
      this.uniqueID = uuidv4()
      this.captchaLink = process.env.VUE_APP_SERVICE_URL + 'captchaImg?uniqueId=' + this.uniqueID
      this.$emit('update:isCaptchaVerified.verified', false)
    },
    updateItems () {
      const thisVar = this
      const func = function (thiz) {
        return new Promise((resolve, reject) => {
          HTTP.get('verifyCaptcha', {params: {captchaCode: thisVar.captchaCode, uniqueId: thisVar.uniqueID}})
            .catch(e => {
              thisVar.isVerified.verified = false
              thisVar.isVerified.captchaCode = ''
              thisVar.$emit('update:isCaptchaVerified', thisVar.isVerified)
              PromiseRejectionEvent.reject()
            })
            .then(e => {
              thisVar.isVerified.verified = e.data.isCaptchaVerified
              thisVar.isVerified.captchaCode = thisVar.captchaCode
              thisVar.$emit('update:isCaptchaVerified', thisVar.isVerified)
              resolve()
            })
        })
      }
      thisVar.isVerified.verifyEvent = func
      thisVar.$emit('update:isCaptchaVerified', thisVar.isVerified)
    }
  },
  mounted () {
    this.getUniqueId()
    this.updateItems()
  }
}
</script>
<template>
  <div class="form bayiFormArea">
    <div class="form-body">
      <div class="input-group">
        <div>
        <img v-bind:src="captchaLink" id="captcha_image" class="dogrulamaKoduImg">
        <button @click="getUniqueId()" title="Yenile" class="captchaRefresh"><img src="https://portaltest.efinans.com.tr/yonetim/refresh.jpg"  alt="Yenile"></button>
        </div>
        <input @blur="updateItems()"  type="text" name="captchaCode" id="captchaCode" placeholder="Doğrulama Kodu" v-model="captchaCode">
      </div>
    </div>
  </div>
</template>



